import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { UserService } from '../user.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import {FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import { MatTableDataSource, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatTabChangeEvent} from '@angular/material';
import {DialogOverviewComponent} from '../createshop/createshop.component';
import * as $ from 'jquery';

const valnumber = /^[0-9][0-9]*$/;
const eml = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent implements OnInit {
  status: any;
  usersList: Array<any>;
  userList: Array<any>;
    url = '/api/';
    total: number = 0;

  constructor(private userService: UserService, private spinner: NgxSpinnerService, private router: Router, 
    public dialog: MatDialog) {
      this.spinner.show();
    this.usersdata('ALL');
    }

  ngOnInit() {
      var userdata = JSON.parse(localStorage.getItem('userDetails'));
      console.log(userdata);
      if(userdata.data[0].super_user && userdata.data[0].super_user != 'Y'){
          this.router.navigate(['dashboard']);
      }
    $('.mat-tab-label-container').css('background','#fff');

    //Close the dropdown if the user clicks outside of it
    window.onclick = function(event) {
      if (!(event.target as HTMLInputElement).matches('.dropbtn')) {
          var dropdowns = document.getElementsByClassName("dropdown-content");
          var i;
          for (i = 0; i < dropdowns.length; i++) {
              var openDropdown = dropdowns[i];
              if (openDropdown.classList.contains('show')) {
                  openDropdown.classList.remove('show');
              }
          }
      }

      
    }
  }

  onTabClick (event: MatTabChangeEvent){
    var index = event.index;
    //var status = 'ALL';
    var status: any;
    if(index == 1){
      status = ['Y'];
    }else if (index == 2){
      status = ['N'];
    }else {
      status = 'ALL';
    }

    this.usersdata(status);
}

showDropdown(id) {
  var dropdowns = document.getElementsByClassName("dropdown-content");
          
          for(var j = 0; j < dropdowns.length; j++){
              var openDropdown = dropdowns[j];
              if (openDropdown.classList.contains('show')) {
                  openDropdown.classList.remove('show');
              }
              
          }
  //$(".myDropdown"+id).toggle();
  $(".myDropdown"+id).addClass('show');
  }

  usersdata (status){
    this.spinner.show();
    const data = {
      status : status
  }
  this.userService.readusers('/api/',data).subscribe(data => {
      this.usersList = data.data;
      //console.log(this.usersList);
      
      this.spinner.hide();
  });
}

public deactivateUser(id) {
  const dialogRef = this.dialog.open(CnfDeactusersDialogComponent, {
      width: '450px',
      data: {
          agent_id: id
      }
  });

  dialogRef.afterClosed().subscribe(result => {
  });
}

public activateUser(id) {
  const dialogRef = this.dialog.open(CnfActusersDialogComponent, {
      width: '450px',
      data: {
          agent_id: id
      }
  });

  dialogRef.afterClosed().subscribe(result => {
  });
}

}

@Component({
  // selector: 'app-cnfActdDctDialog',
  templateUrl: './userlist-cnfdact-dialog-component.html',
  styleUrls: ['./userlist-cnfdact-dialog-component.scss']
})
export class CnfDeactusersDialogComponent {
  cstatus: any;
  url; '/api/';
  constructor(
      public dialogRef: MatDialogRef<CnfDeactusersDialogComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

  onNoClick(): void {
      this.dialogRef.close();
  }

  onDeacativateClick(userid): void {
      const dact_user = {
          user_id: userid,
          change_status: 'N',
          db_query: 'change_status'
      };
      this.userService.edituserbyid(this.url, dact_user)
          .subscribe(
              data => {
                  if (data.status === true) {
                      this.cstatus = 'User De-Activated Successfully !';
                  } else {
                      this.cstatus = 'Unable to De-Activate Please try again !';
                  }
                  this.dialog.open(DialogOverviewComponent, {
                      width: '450px',
                      data: { status: this.cstatus }
                  });
              }

          );
      this.dialogRef.close();
  }

}

@Component({
  // selector: 'app-cnfActdDctDialog',
  templateUrl: './userlist-cnfact-dialog-component.html',
  styleUrls: ['./userlist-cnfact-dialog-component.scss']
})
export class CnfActusersDialogComponent {
  cstatus: any;
  url; '/api/';
  constructor(
      public dialogRef: MatDialogRef<CnfActusersDialogComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

  onNoClick(): void {
      this.dialogRef.close();
  }

  onactivateClick(userid): void {
      const act_user = {
          user_id: userid,
          change_status: 'Y',
          db_query: 'change_status'
      };
      this.userService.edituserbyid(this.url, act_user)
          .subscribe(
              data => {
                  if (data.status === true) {
                      this.cstatus = 'Agent Activated Successfully !';
                  } else {
                      this.cstatus = 'Unable to Activate Please try again !';
                  }
                  this.dialog.open(DialogOverviewComponent, {
                      width: '450px',
                      data: { status: this.cstatus }
                  });
              }

          );
      this.dialogRef.close();
  }

}





