import { Component, HostBinding, OnInit, ChangeDetectorRef, Input } from '@angular/core';
import {AbstractControl, FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import { discountSideBarService } from './discounts-sidebar.service';
import { UserService } from '../user.service';

@Component({
  selector: 'app-discounts-sidebar',
  templateUrl: './discounts-sidebar.component.html',
  styleUrls: ['./discounts-sidebar.component.scss']
})
export class DiscountsSidebarComponent implements OnInit {
  @HostBinding('class.is-discountOpen')
  isDiscountOpen = false;
  url = '/api/';
  cstatus: string;
  activeShopsList: Array<any>;
  shopsList: Array<any>;
  fileToUpload: File = null;

  constructor(private userService: UserService, private cd: ChangeDetectorRef, private discountSidebar: discountSideBarService) { }

  // For Create Category
  createDiscountForm = new FormGroup({
    description: new FormControl('', Validators.compose([Validators.minLength(3)])),
    actualPrice: new FormControl('',Validators.compose([Validators.required,this.noWhitespaceValidator])),
    validFrom: new FormControl('',Validators.compose([Validators.required,this.noWhitespaceValidator])),
    validTo: new FormControl('',Validators.compose([Validators.required,this.noWhitespaceValidator])),
    code: new FormControl('',Validators.compose([Validators.required,this.noWhitespaceValidator])),
    offPercentage: new FormControl('',Validators.compose([Validators.required,this.noWhitespaceValidator])),
    specialPrice: new FormControl('',Validators.compose([Validators.required,this.noWhitespaceValidator])),
    shopid: new FormControl('',Validators.compose([Validators.required]))
});

public noWhitespaceValidator(control: FormControl) {
  let isWhitespace = (control.value || '').trim().length < 1;
  let isValid = !isWhitespace;
  return isValid ? null : { 'whitespace': true };
}

  ngOnInit() {
    this.discountSidebar.discountchange.subscribe(DiscountOpen => {
      this.isDiscountOpen = DiscountOpen;
      this.shopsdata();
    });
  }

  // close create category menu
  discountclose(){
    this.discountSidebar.discountSidebarClose();
  }

  // Function to read shops
  shopsdata (){

  this.userService.readshops('/api/','').subscribe(data => {
      this.activeShopsList = data.filter(shops => shops.active == true);

      this.shopsList = [];
      for (let i = 0; i < this.activeShopsList.length; i++){
        this.shopsList.push({value: this.activeShopsList[i].id, viewValue: this.activeShopsList[i].name});
      }
      this.cd.detectChanges();
      
  });
  }

  handleFileInput(files: FileList) {
    this.fileToUpload = files.item(0);
}

  // Function For Create discont
  onCreateDiscountSubmit(createDiscountForm,formDirective) {
    let userdetails = {
      email: localStorage.getItem('useremail'),
      password: localStorage.getItem('password')
  }
    
    let reqdata = {
      description: this.createDiscountForm.get('description').value,
      actualPrice: parseInt(this.createDiscountForm.get('actualPrice').value),
      validFrom: this.createDiscountForm.get('validFrom').value,
      validTo: this.createDiscountForm.get('validTo').value,
      code: this.createDiscountForm.get('code').value,
      offPercentage: parseInt(this.createDiscountForm.get('offPercentage').value),
      specialPrice: parseInt(this.createDiscountForm.get('specialPrice').value),
      shopid: parseInt(this.createDiscountForm.get('shopid').value),
      image: null
    }
    this.userService.createDiscount(userdetails, reqdata)
        .subscribe(
            (data) => {
              console.log(data);
                if (data) {
                    this.cstatus = 'Discount Added Successfully !';
                    formDirective.resetForm();
                    createDiscountForm.reset();
                }
                
            },
            (err) => {
              console.log(err);
              this.cstatus = '<span style="color:red">'+err.error.message+'</span>';
            }

        );
}

}



