import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { UserService } from '../user.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import {FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import { MatTableDataSource, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatTabChangeEvent} from '@angular/material';
import {DialogOverviewComponent} from '../createshop/createshop.component';
import { categorySideBarService } from '../category-sidebar/category-sidebar.service';
import { subcategorySideBarService } from '../subcategory-sidebar/subcategory-sidebar.service';
import { discountSideBarService } from '../discounts-sidebar/discounts-sidebar.service';
import * as $ from 'jquery';

const valnumber = /^[0-9][0-9]*$/;
const eml = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

@Component({
  selector: 'app-agents',
  templateUrl: './agents.component.html',
  styleUrls: ['./agents.component.scss']
})
export class AgentsComponent implements OnInit {
  status: any;
  agentsList: Array<any>;
  activeAgentsList: Array<any>;
  deactiveAgentsList: Array<any>;
  agentList: Array<any>;
    url = '/api/';

  constructor(private userService: UserService, private spinner: NgxSpinnerService, private router: Router, 
    public dialog: MatDialog, private categorysidebar: categorySideBarService, private subcategorysidebar: subcategorySideBarService,
    private discountsidebar: discountSideBarService) {
      this.spinner.show();
    this.agentsdata();
    }

  ngOnInit() {
    this.categorysidebar.categorySidebarClose();
    this.subcategorysidebar.subcategorySidebarClose();
    this.discountsidebar.discountSidebarClose();
    $('.mat-tab-label-container').css('background','#fff');

    //Close the dropdown if the user clicks outside of it
    window.onclick = function(event) {
      //console.log(event);
      if (!(event.target as HTMLInputElement).matches('.dropbtn')) {
          var dropdowns = document.getElementsByClassName("dropdown-content");
          var i;
          for (i = 0; i < dropdowns.length; i++) {
              var openDropdown = dropdowns[i];
              if (openDropdown.classList.contains('show')) {
                  openDropdown.classList.remove('show');
              }
          }
      }

      
    }
  }

  onTabClick (event: MatTabChangeEvent){
    var index = event.index;
}

  agentsdata (){
      this.spinner.show();
    this.userService.readagents('/api/').subscribe(data => {
        this.agentsList = data;
        this.activeAgentsList = data.filter(agents => agents.active == true);
        this.deactiveAgentsList = data.filter(agents => agents.active == false);
        console.log(this.agentsList);
        
        this.spinner.hide();
    });
  }

  showDropdown(id) {
    var dropdowns = document.getElementsByClassName("dropdown-content");
            
            for(var j = 0; j < dropdowns.length; j++){
                var openDropdown = dropdowns[j];
                if (openDropdown.classList.contains('show')) {
                    openDropdown.classList.remove('show');
                }
                
            }
    //$(".myDropdown"+id).toggle();
    $(".myDropdown"+id).addClass('show');
    }

    public editData(id) {
      const agentid = {
          agent_id: id
      };
      this.spinner.show();
      this.userService.readagentbyid(this.url, agentid).subscribe(data => {
          this.agentList = data.data;
          this.spinner.hide();
          this.editagentDialog();
      });
  }

  // Function to edit agent data
  editagentDialog(): void {
      const dialogRef = this.dialog.open(EditagentsDialogComponent, {
          width: '350px',
          data:  this.agentList[0]
      });

      dialogRef.afterClosed().subscribe(result => {
          // console.log('The dialog was closed');
          // this.cstatus = result;
      });
  }

  public deactivateAgent(id) {
      const dialogRef = this.dialog.open(CnfDeactagntsDialogComponent, {
          width: '450px',
          data: {
              agent_id: id
          }
      });

      dialogRef.afterClosed().subscribe(result => {
          // console.log('The dialog was closed');
          // this.cstatus = result;
      });
  }

  public activateAgent(id) {
      const dialogRef = this.dialog.open(CnfActagntsDialogComponent, {
          width: '450px',
          data: {
              agent_id: id
          }
      });

      dialogRef.afterClosed().subscribe(result => {
          // console.log('The dialog was closed');
          // this.cstatus = result;
      });
  }

}

@Component({
  // selector: 'app-cnfActdDctDialog',
  templateUrl: './agentlist-cnfdact-dialog-component.html',
  styleUrls: ['./agentlist-cnfdact-dialog-component.scss']
})
export class CnfDeactagntsDialogComponent {
  cstatus: any;
  url; '/api/';
  constructor(
      public dialogRef: MatDialogRef<CnfDeactagntsDialogComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

  onNoClick(): void {
      this.dialogRef.close();
  }

  onDeacativateClick(agentid): void {
      let reqdata = {
          active: false,
          id: agentid
      }
      this.userService.activeDeactiveAgentById(this.url, reqdata)
          .subscribe(
              (data) => {
                  this.cstatus = 'Agent De-Activated Successfully !';
                  this.dialog.open(DialogOverviewComponent, {
                      width: '450px',
                      data: { status: this.cstatus }
                  });
              },
              (err) => {
                  console.log(err);
                this.cstatus = err.error.message;
                this.dialog.open(DialogOverviewComponent, {
                    width: '450px',
                    data: { status: this.cstatus }
                });
              }

          );
      this.dialogRef.close();
  }

}

@Component({
  // selector: 'app-cnfActdDctDialog',
  templateUrl: './agentlist-cnfact-dialog-component.html',
  styleUrls: ['./agentlist-cnfact-dialog-component.scss']
})
export class CnfActagntsDialogComponent {
  cstatus: any;
  url; '/api/';
  constructor(
      public dialogRef: MatDialogRef<CnfActagntsDialogComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

  onNoClick(): void {
      this.dialogRef.close();
  }

  onactivateClick(agentid): void {
    let reqdata = {
        active: true,
        id: agentid
    }
    this.userService.activeDeactiveAgentById(this.url, reqdata)
          .subscribe(
              (data) => {
                  this.cstatus = 'Agent Activated Successfully !';
                  this.dialog.open(DialogOverviewComponent, {
                      width: '450px',
                      data: { status: this.cstatus }
                  });
              },
              (err) => {
                  console.log(err);
                  this.cstatus = err.error.message;
                  this.dialog.open(DialogOverviewComponent, {
                      width: '450px',
                      data: { status: this.cstatus }
                  });
              }

          );
      this.dialogRef.close();
  }

}

@Component({
  selector: 'app-dialog',
  templateUrl: 'editagent-dialog-component.html',
  styleUrls: ['./editagent-dialog-component.scss']
})
export class EditagentsDialogComponent implements OnInit {
  cstatus: any;
  url = '/api/';
  shopList: Array<any>;

  @ViewChild(FormGroupDirective) myform;
  // For Edit shop
  editAgentForm = new FormGroup({
      userId: new FormControl(),
      name: new FormControl('', Validators.compose([Validators.minLength(3)])),
      email: new FormControl('', Validators.pattern(eml)),
      phone: new FormControl('', Validators.compose([Validators.maxLength(12),
          Validators.minLength(8), Validators.pattern(valnumber)])),
      primary_area: new FormControl('', Validators.compose([Validators.minLength(3)])),
      secondary_area: new FormControl(),
      tertiary_area: new FormControl(),
      daily_targets: new FormControl('', Validators.compose([Validators.minLength(1)])),
      agentid: new FormControl()
  });

  ngOnInit() {

  }

  /*openDialog(): void {
      const dialogRef = this.dialog.open(AgentlistDialogComponent, {
          width: '450px',
          data:  this.shopList[0]
      });
      // this.editShopForm.setValue(this.dialogRef.componentInstance.data);
      dialogRef.afterClosed().subscribe(result => {
          // console.log('The dialog was closed');
          // this.cstatus = result;
      });
  }*/

  // Function For Edit shop
  onEditAgentSubmit() {
      const userdata = JSON.parse(localStorage.getItem('userDetails'));
      const shopdata = {
          user_id: userdata.data[0].id,
          name: this.editAgentForm.get('name').value,
          email: this.editAgentForm.get('email').value,
          phone: this.editAgentForm.get('phone').value,
          primary_area: this.editAgentForm.get('primary_area').value,
          secondary_area: this.editAgentForm.get('secondary_area').value,
          tertiary_area: this.editAgentForm.get('tertiary_area').value,
          daily_targets: this.editAgentForm.get('daily_targets').value,
          db_query: 'update',
          agent_id: this.editAgentForm.get('agentid').value
      };
      this.userService.editagentbyid(this.url, shopdata)
          .subscribe(
              data => {
                  if (data.status === true) {
                      this.cstatus = 'Agent Updated Successfully !';
                  } else {
                      this.cstatus = 'Unable to update Please try again !';
                  }
                  this.updateDialog();
              }

          );
  }

  updateDialog(): void {
      const dialogRef = this.dialog.open(DialogOverviewComponent, {
          width: '450px',
          data: { status: this.cstatus }
      });

      dialogRef.afterClosed().subscribe(result => {
          // this.cstatus = result;
      });
  }

  constructor(
      public dialogRef: MatDialogRef<EditagentsDialogComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

  onNoClick(): void {
      this.dialogRef.close();
  }
}



