import { Component, HostBinding, OnInit, ChangeDetectorRef, Input } from '@angular/core';
import {AbstractControl, FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import { subscriptionplanSideBarService } from './subscriptionplan-sidebar.service';
import { UserService } from '../user.service';

@Component({
  selector: 'app-subscriptionplan-sidebar',
  templateUrl: './subscriptionplan-sidebar.component.html',
  styleUrls: ['./subscriptionplan-sidebar.component.scss']
})
export class SubscriptionplanSidebarComponent implements OnInit {
  @HostBinding('class.is-subscriptionPlanOpen')
  isSubscriptionplanOpen = false;
  url = '/api/';
  cstatus: string;

  planFor = [
    {'text':'Agent', 'value':'agent'},
    {'text':'shop Owner', 'value':'shopOwner'}
  ];
  planType = [
    {'text':'Base', 'value':'base'},
    {'text':'Topup', 'value':'topup'}
  ]

  constructor(private userService: UserService, private cd: ChangeDetectorRef, private subscriptionplanSidebar: subscriptionplanSideBarService) { }

  createSubscriptionPlanForm = new FormGroup({
    name: new FormControl('', Validators.compose([Validators.minLength(1)])),
    planfor: new FormControl('', Validators.compose([Validators.minLength(1)])),
    plantype: new FormControl('', Validators.compose([Validators.minLength(1)])),
    credits: new FormControl('', Validators.compose([Validators.minLength(1)])),
    validity: new FormControl('', Validators.compose([Validators.minLength(1)])),
    price: new FormControl('', Validators.compose([Validators.minLength(1)]))
    //actualPrice: new FormControl('',Validators.compose([Validators.required,this.noWhitespaceValidator])),
    
});

// Function For Create discont
onCreateSubscriptionPlanSubmit(createSubscriptionPlanForm,formDirective) {
  let userdetails = {
    email: localStorage.getItem('useremail'),
    password: localStorage.getItem('password')
}
  
  let reqdata = {
    identifier: 1,
    name: this.createSubscriptionPlanForm.get('name').value,
    for: this.createSubscriptionPlanForm.get('planfor').value,
    planType: this.createSubscriptionPlanForm.get('plantype').value,
    credits: this.createSubscriptionPlanForm.get('credits').value,
    validity: this.createSubscriptionPlanForm.get('validity').value,
    price: this.createSubscriptionPlanForm.get('price').value,
    
  }
  this.userService.createSubscriptionPlan(userdetails, reqdata)
      .subscribe(
          (data) => {
            console.log(data);
              if (data) {
                  this.cstatus = 'Subscription Plan Added Successfully !';
                  formDirective.resetForm();
                  createSubscriptionPlanForm.reset();
              }
              
          },
          (err) => {
            console.log(err);
            this.cstatus = '<span style="color:red">'+err.error.message+'</span>';
          }

      );
}

  ngOnInit() {
    this.subscriptionplanSidebar.subscriptionplanchange.subscribe(SubscriptionPlanOpen => {
      this.isSubscriptionplanOpen = SubscriptionPlanOpen;
      //this.shopsdata();
    });
  }

  // close create sub category menu
  subscriptionplanclose(){
    this.subscriptionplanSidebar.subscriptionplanSidebarClose();
  }

}



