import {Component, Inject, OnInit, ViewChild} from '@angular/core';
import { UserService } from '../user.service';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import { MatTableDataSource, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { Observable } from 'rxjs/Observable';
import {FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import {DialogOverviewComponent} from '../createshop/createshop.component';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-viewcategories',
  templateUrl: './viewcategories.component.html',
  styleUrls: ['./viewcategories.component.scss']
})
export class ViewcategoriesComponent implements OnInit{
    status: any;
    categoriesList: Array<any>;
    categoryList: Array<any>;
    message: any;
    url = '/api/';
    dataSource: MatTableDataSource<any>;

    @ViewChild(MatSort) sort: MatSort;
    displayedColumns = ['category', 'status', 'action'];
    applyFilter(filterValue: string) {
        filterValue = filterValue.trim(); // Remove whitespace
        filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
        this.dataSource.filter = filterValue;
    }

    constructor(private userService: UserService, private router: Router, public dialog: MatDialog, private spinner: NgxSpinnerService) {
        this.spinner.show();
        this.getCategories();
    }

    ngOnInit(){
        //Close the dropdown if the user clicks outside of it
        window.onclick = function(event) {
            if (!(event.target as HTMLInputElement).matches('.dropbtn')) {
                var dropdowns = document.getElementsByClassName("dropdown-content");
                var i;
                for (i = 0; i < dropdowns.length; i++) {
                    var openDropdown = dropdowns[i];
                    if (openDropdown.classList.contains('show')) {
                        openDropdown.classList.remove('show');
                    }
                }
            }
            
        }
    }

    showDropdown(id) {
        var dropdowns = document.getElementsByClassName("dropdown-content");
                
                for(var j = 0; j < dropdowns.length; j++){
                    var openDropdown = dropdowns[j];
                    if (openDropdown.classList.contains('show')) {
                        openDropdown.classList.remove('show');
                    }
                }
        $(".myDropdown"+id).addClass('show');
    }

    public getCategories() {
        const data = {
            status : 'ALL'
        }
        this.userService.readCategories('/api/').subscribe(data => {
            this.categoriesList = data.data;
            this.dataSource = new MatTableDataSource(this.categoriesList);
            this.dataSource.sort = this.sort;
            this.spinner.hide();
        });
    }

    public editData(id) {
        const categoryid = {
            category_id: id
        };
        this.spinner.show();
        this.userService.readCategories(this.url).subscribe(data => {
            this.categoryList = data.data;
            this.spinner.hide();
            this.editcategoryDialog();
        });
    }

    // Function to edit category data
    editcategoryDialog(): void {
        const dialogRef = this.dialog.open(EditcategoryDialogComponent, {
            width: '450px',
            data:  this.categoryList[0]
        });

        dialogRef.afterClosed().subscribe(result => {
            // console.log('The dialog was closed');
            // this.cstatus = result;
        });
    }

    // Function to block category
    public blockData(id) {
        const categoryid = {
            category_id: id,
            db_query: 'change_status',
            change_status: 'N'
        };
        this.userService.editCategorybyid(this.url, categoryid).subscribe(data => {
            this.message = 'Category Blocked !';
            this.modifycategoryDialog();
        });
    }

    // Function to unblock category
    public unblockData(id) {
        const categoryid = {
            category_id: id,
            db_query: 'change_status',
            change_status: 'Y'
        };
        this.userService.editCategorybyid(this.url, categoryid).subscribe(data => {
            this.message = 'Category Activated!';
            this.modifycategoryDialog();
        });
    }

    modifycategoryDialog(): void {
        const dialogRef = this.dialog.open(CategorylistActDactDialogComponent, {
            width: '450px',
            data:  this.message
        });

        dialogRef.afterClosed().subscribe(result => {
            // console.log('The dialog was closed');
            // this.cstatus = result;
        });
    }

}

@Component({
    selector: 'app-dialog',
    templateUrl: 'editcategory-dialog-component.html',
    styleUrls: ['./editcategory-dialog-component.scss']
})
export class EditcategoryDialogComponent implements OnInit {
    cstatus: any;
    url = '/api/';
    categoryList: Array<any>;

    @ViewChild(FormGroupDirective) myform;
    // For Edit shop
    editCategoryForm = new FormGroup({
        status: new FormControl(),
        category: new FormControl('', Validators.compose([Validators.minLength(3)])),
        categoryid: new FormControl()
    });

    ngOnInit() {

    }


    // Function For Edit category
    onEditCategorySubmit() {
        const userdata = JSON.parse(localStorage.getItem('userDetails'));
        const categorydata = {
            updated_by: userdata.data[0].id,
            name: this.editCategoryForm.get('category').value,
            db_query: 'update',
            category_id: this.editCategoryForm.get('categoryid').value
        };
        this.userService.editCategorybyid(this.url, categorydata)
            .subscribe(
                data => {
                    if (data.status === true) {
                        this.cstatus = 'Category Updated Successfully !';
                    } else {
                        this.cstatus = 'Unable to update Please try again !';
                    }
                    this.updateDialog();
                }

            );
    }

    updateDialog(): void {
        const dialogRef = this.dialog.open(DialogOverviewComponent, {
            width: '450px',
            data: { status: this.cstatus }
        });

        dialogRef.afterClosed().subscribe(result => {
            // this.cstatus = result;
        });
    }

    constructor(
        public dialogRef: MatDialogRef<EditcategoryDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

    onNoClick(): void {
        this.dialogRef.close();
    }

}

@Component({
    selector: 'app-dialog',
    templateUrl: 'categorylist-dialog-component.html',
    styleUrls: ['./categorylist-dialog-component.scss']
})
export class CategorylistDialogComponent {

    constructor(
        public dialogRef: MatDialogRef<CategorylistDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any) { }

    onNoClick(): void {
        this.dialogRef.close();
    }

}

@Component({
    selector: 'app-dialog',
    templateUrl: 'categorylistActDact-dialog-component.html',
    styleUrls: ['./categorylistActDact-dialog-component.scss']
})
export class CategorylistActDactDialogComponent {

    constructor(
        public dialogRef: MatDialogRef<CategorylistActDactDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any) { }

    onNoClick(): void {
        this.dialogRef.close();
        location.reload();
    }

}








