import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { UserService } from '../user.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import {AbstractControl, FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import { MatTableDataSource, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatTabChangeEvent, MatSidenav} from '@angular/material';
import {DialogOverviewComponent} from '../createshop/createshop.component';
import * as $ from 'jquery';
import { Observable } from 'rxjs';
import { categorySideBarService } from '../category-sidebar/category-sidebar.service';
import { subcategorySideBarService } from '../subcategory-sidebar/subcategory-sidebar.service';
import { discountSideBarService } from '../discounts-sidebar/discounts-sidebar.service';

@Component({
  selector: 'app-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.scss']
})
export class CategoriesComponent implements OnInit {
  status: any;
  categoriesList: Array<any>;
  activeCategoriesList: Array<any>;
  deactiveCategoriesList: Array<any>;
  categoryList: Array<any>;
  message: any;
  url = '/api/';
  cstatus: string;
  breakpoint: number;
  public categorytoggle: Observable <boolean>;

  //@ViewChild(FormGroupDirective) myform;
  @ViewChild('categoryleft') togglecategory: MatSidenav;

    // For Create Category
    createCategoryForm = new FormGroup({
        category: new FormControl('', Validators.compose([Validators.minLength(3),this.noWhitespaceValidator]))
    });

    public noWhitespaceValidator(control: FormControl) {
        let isWhitespace = (control.value || '').trim().length < 3;
        let isValid = !isWhitespace;
        return isValid ? null : { 'whitespace': true };
    }

    constructor(private userService: UserService, private spinner: NgxSpinnerService, private router: Router, 
      public dialog: MatDialog,private formDirective: FormGroupDirective, private userservice: UserService,
        private categorysidebar: categorySideBarService, private subcategorysidebar: subcategorySideBarService,
        private discountsidebar: discountSideBarService) {
        this.spinner.show();
        this.categoriesdata();
      }

      categoryopen(){
        this.categorysidebar.categorySidebarOpen();
      }

      // Function For Create category
    onCreateCategorySubmit(createCategoryForm,formDirective) {
        const userdata = JSON.parse(localStorage.getItem('userDetails'));
        const categorydata = {
            created_by: userdata.data[0].id,
            name: this.createCategoryForm.get('category').value,
            status: 'Y'
        };
        this.userService.createCategory(this.url, categorydata)
            .subscribe(
                data => {
                    if (data.status === true) {
                        this.cstatus = 'Category Added Successfully !';
                        formDirective.resetForm();
                        createCategoryForm.reset();
                    } else {
                        this.cstatus = '<span>Category Already exists !</span>';
                    }
                    
                }

            );
    }

    onResize(event) {
        this.breakpoint = (event.target.innerWidth <= 400) ? 12 : 3;
      }
      ngOnInit() {
        this.breakpoint = (window.innerWidth <= 400) ? 12 : 3;
        this.categorysidebar.categorySidebarClose();
        this.subcategorysidebar.subcategorySidebarClose();
        this.discountsidebar.discountSidebarClose();
        $('.mat-tab-label-container').css('background','#fff');
    
        //Close the dropdown if the user clicks outside of it
        window.onclick = function(event) {
          //console.log(event);
          if (!(event.target as HTMLInputElement).matches('.dropbtn')) {
              var dropdowns = document.getElementsByClassName("dropdown-content");
              var i;
              for (i = 0; i < dropdowns.length; i++) {
                  var openDropdown = dropdowns[i];
                  if (openDropdown.classList.contains('show')) {
                      openDropdown.classList.remove('show');
                  }
              }
          }
    
          
        }
      }

      onTabClick (event: MatTabChangeEvent){
        var index = event.index;
    }

    categoriesdata (){
        this.spinner.show();
 
      this.userService.readCategories('/api/').subscribe(data => {
          this.categoriesList = data.filter(category => category.parentid == null);
          console.log(this.categoriesList);
          
          this.activeCategoriesList = this.categoriesList.filter(category => category.active == true);
          this.deactiveCategoriesList = this.categoriesList.filter(category => category.active == false);
          
          this.spinner.hide();
      });
    }

    showDropdown(id) {
      var dropdowns = document.getElementsByClassName("dropdown-content");
              
              for(var j = 0; j < dropdowns.length; j++){
                  var openDropdown = dropdowns[j];
                  if (openDropdown.classList.contains('show')) {
                      openDropdown.classList.remove('show');
                  }
                  
              }
      //$(".myDropdown"+id).toggle();
      $(".myDropdown"+id).addClass('show');
      }

      public editData(id) {
        this.spinner.show();
        let selectedCategory = this.categoriesList.find(category => category.id == id);
        console.log(selectedCategory);
        this.categoryList = selectedCategory;
        this.spinner.hide();
        this.editcategoryDialog();
    }

    // Function to edit category data
    editcategoryDialog(): void {
        const dialogRef = this.dialog.open(EditcategoriesDialogComponent, {
            width: '450px',
            data:  this.categoryList
        });

        dialogRef.afterClosed().subscribe(result => {
            // console.log('The dialog was closed');
            // this.cstatus = result;
        });
    }

    // Function to block category
    public blockData(id) {
        let reqdata = {
            id: id,
            active: false
        }
        this.userService.activeDeactiveCategorybyid(this.url, reqdata).subscribe(data => {
            this.message = 'Category Blocked !';
            this.modifycategoryDialog();
        });
    }

    // Function to unblock category
    public unblockData(id) {
        let reqdata = {
            id: id,
            active: true
        }
        this.userService.activeDeactiveCategorybyid(this.url, reqdata).subscribe(data => {
            this.message = 'Category Activated!';
            this.modifycategoryDialog();
        });
    }

    modifycategoryDialog(): void {
      const dialogRef = this.dialog.open(CategorieslistActDactDialogComponent, {
          width: '450px',
          data:  this.message
      });

      dialogRef.afterClosed().subscribe(result => {
          // console.log('The dialog was closed');
          // this.cstatus = result;
      });
  }



}

@Component({
  selector: 'app-dialog',
  templateUrl: 'editcategory-dialog-component.html',
  styleUrls: ['./editcategory-dialog-component.scss']
})
export class EditcategoriesDialogComponent implements OnInit {
  cstatus: any;
  url = '/api/';
  categoryList: Array<any>;

  @ViewChild(FormGroupDirective) myform;
  // For Edit shop
  editCategoryForm = new FormGroup({
      status: new FormControl(),
      category: new FormControl('', Validators.compose([Validators.minLength(3)])),
      categoryid: new FormControl()
  });

  ngOnInit() {
  }


  // Function For Edit category
  onEditCategorySubmit() {
      let reqdata = {
          id: this.editCategoryForm.get('categoryid').value,
          text: this.editCategoryForm.get('category').value
      }

      this.userService.editCategorybyid(this.url, reqdata)
          .subscribe(
              (data) => {
                this.cstatus = 'Category Updated Successfully !';
                this.updateDialog();
              },
              (err) => {
                this.cstatus = err.error.message;
                this.updateDialog();
              }

          );
  }

  updateDialog(): void {
      const dialogRef = this.dialog.open(DialogOverviewComponent, {
          width: '450px',
          data: { status: this.cstatus }
      });

      dialogRef.afterClosed().subscribe(result => {
          // this.cstatus = result;
      });
  }

  constructor(
      public dialogRef: MatDialogRef<EditcategoriesDialogComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

  onNoClick(): void {
      this.dialogRef.close();
  }

}

@Component({
  selector: 'app-dialog',
  templateUrl: 'categorylist-dialog-component.html',
  styleUrls: ['./categorylist-dialog-component.scss']
})
export class CategorieslistDialogComponent {

  constructor(
      public dialogRef: MatDialogRef<CategorieslistDialogComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any) { }

  onNoClick(): void {
      this.dialogRef.close();
  }

}

@Component({
  selector: 'app-dialog',
  templateUrl: 'categorylistActDact-dialog-component.html',
  styleUrls: ['./categorylistActDact-dialog-component.scss']
})
export class CategorieslistActDactDialogComponent {

  constructor(
      public dialogRef: MatDialogRef<CategorieslistActDactDialogComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any) { }

  onNoClick(): void {
      this.dialogRef.close();
      location.reload();
  }

}



