import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { Location } from '@angular/common';
import { UserService } from '../user.service';
import { AbstractControl, FormControl, Validators, FormGroup, FormGroupDirective } from '@angular/forms';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {DialogOverviewComponent} from '../createshop/createshop.component';
import { categorySideBarService } from '../category-sidebar/category-sidebar.service';
import { subcategorySideBarService } from '../subcategory-sidebar/subcategory-sidebar.service';

@Component({
  selector: 'app-createcategory',
  templateUrl: './createcategory.component.html',
  styleUrls: ['./createcategory.component.scss']
})
export class CreatecategoryComponent implements OnInit {

    url = '/api/';
    cstatus: string;

    @ViewChild(FormGroupDirective) myform;

    // For Create Category
    createCategoryForm = new FormGroup({
        category: new FormControl('', Validators.compose([Validators.minLength(3)]))
    });

    constructor(private userService: UserService, public dialog: MatDialog, private location: Location,
        private categorysidebar: categorySideBarService, private subcategorysidebar: subcategorySideBarService) { }

    ngOnInit() {
        this.categorysidebar.categorySidebarClose();
        this.subcategorysidebar.subcategorySidebarClose();
    }

    goback(){
        this.location.back();
    }

    // Function For Create category
    onCreateCategorySubmit() {
        const userdata = JSON.parse(localStorage.getItem('userDetails'));
        console.log(userdata);
        const categorydata = {
            created_by: userdata.data[0].id,
            name: this.createCategoryForm.get('category').value,
            status: 'Y'
        };
        this.userService.createCategory(this.url, categorydata)
            .subscribe(
                data => {
                    if (data.status === true) {
                        this.cstatus = 'Category Added Successfully !';
                    } else {
                        this.cstatus = 'Category Already exists !';
                    }
                    this.openDialog();
                }

            );
    }

    openDialog(): void {
        let dialogRef = this.dialog.open(DialogOverviewComponent, {
            width: '450px',
            data: { status: this.cstatus }
        });

        dialogRef.afterClosed().subscribe(result => {
            console.log('The dialog was closed');
            this.cstatus = result;
        });
    }

}


