import { Injectable, EventEmitter, Output, Component, Input } from '@angular/core';
@Injectable()
export class subscriptionplanSideBarService {

  isSubscriptionplanOpen = false;

  @Output() subscriptionplanchange: EventEmitter<boolean> = new EventEmitter();

  toggle() {
    this.isSubscriptionplanOpen = !this.isSubscriptionplanOpen;
    this.subscriptionplanchange.emit(this.isSubscriptionplanOpen);
  }

  subscriptionplanSidebarClose() {
    this.subscriptionplanchange.emit(false);
  }

  subscriptionplanSidebarOpen() {
    this.subscriptionplanchange.emit(true);
  }

}


