import { Component, OnInit, Inject, ViewChild, Pipe, PipeTransform } from '@angular/core';
import { UserService } from '../user.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import {FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import { MatTableDataSource, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatTabChangeEvent} from '@angular/material';
import {DialogOverviewComponent} from '../createshop/createshop.component';
import * as $ from 'jquery';

@Component({
  selector: 'app-subscriptions',
  templateUrl: './subscriptions.component.html',
  styleUrls: ['./subscriptions.component.scss']
})
export class SubscriptionsComponent implements OnInit {
  status: any;
  subscriptionsList: Array<any>;
  subscriptionList: Array<any>;
  url = '/api/';
  message: any;

  constructor(private userService: UserService, private spinner: NgxSpinnerService, private router: Router, 
    public dialog: MatDialog) { 
    this.spinner.show();
    this.subscriptionsdata();
    }

  ngOnInit() {
  }

  subscriptionsdata (){
    this.spinner.show();
  this.userService.readsubscriptions('/api/').subscribe(data => {
      this.subscriptionsList = data;
      console.log(data);

      this.spinner.hide();
  });
}

showDropdown(id) {
  var dropdowns = document.getElementsByClassName("dropdown-content");
          
          for(var j = 0; j < dropdowns.length; j++){
              var openDropdown = dropdowns[j];
              if (openDropdown.classList.contains('show')) {
                  openDropdown.classList.remove('show');
              }
              
          }
  $(".myDropdown"+id).addClass('show');
  }

  updateSubscription(id){
    this.spinner.show();
        let selectedSubscription = this.subscriptionsList.find(subscription => subscription.id == id);
        this.subscriptionList = selectedSubscription;
        console.log(this.subscriptionList);
        this.spinner.hide();
        this.editsubscriptionDialog();
  }

  // Function to edit Subscription data
  editsubscriptionDialog(): void {
    const dialogRef = this.dialog.open(EditsubscriptionsDialogComponent, {
        width: '450px',
        data:  this.subscriptionList
    });
    console.log(this.subscriptionList);

    dialogRef.afterClosed().subscribe(result => {
        // console.log('The dialog was closed');
        // this.cstatus = result;
    });
}

}

@Component({
  selector: 'app-dialog',
  templateUrl: 'editsubscription-dialog-component.html',
  styleUrls: ['./editsubscription-dialog-component.scss']
})
export class EditsubscriptionsDialogComponent implements OnInit {
  cstatus: any;
  url = '/api/';
  subscriptionList: Array<any>;

  @ViewChild(FormGroupDirective) myform;

  editSubscriptionForm = new FormGroup({
    subscriptionid: new FormControl(''),
    credits: new FormControl('', Validators.compose([Validators.minLength(1)])),
    subscribedFor: new FormControl('', Validators.compose([Validators.minLength(1)])),
    subscribedOn: new FormControl('', Validators.compose([Validators.minLength(1)]))
});

onEditSubscriptionSubmit(){

  let subscribedOn = new Date(this.editSubscriptionForm.get('subscribedOn').value);
  let subscribedDate = subscribedOn.getFullYear()+'-'+(subscribedOn.getMonth()+1)+'-'+subscribedOn.getDate();

  let validTo = new Date(this.editSubscriptionForm.get('subscribedOn').value);
  let toDate = validTo.getFullYear()+'-'+(validTo.getMonth()+1)+'-'+validTo.getDate();

  let reqdata = {
      id: this.editSubscriptionForm.get('subscriptionid').value,
      credits: this.editSubscriptionForm.get('credits').value,
      subscribedFor: this.editSubscriptionForm.get('subscribedFor').value,
      subscribedOn: subscribedDate
  }
  
  this.userService.editSubscriptionbyid(this.url, reqdata)
      .subscribe(
          (data) => {
            this.cstatus = 'Subscription Updated Successfully !';
            this.updateDialog();
          },
          (err) => {
              console.log(err);
            this.cstatus = err.error.message;
            this.updateDialog();
          }

      );
      
}

updateDialog(): void {
  const dialogRef = this.dialog.open(DialogOverviewComponent, {
      width: '450px',
      data: { status: this.cstatus }
  });

  dialogRef.afterClosed().subscribe(result => {
      // this.cstatus = result;
  });
}

constructor(
  public dialogRef: MatDialogRef<EditsubscriptionsDialogComponent>,
  @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

onNoClick(): void {
  this.dialogRef.close();
}

  ngOnInit(){

  }
}




