import { Component, Inject, ViewChild } from '@angular/core';
import { UserService } from '../user.service';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import { MatTableDataSource, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {DialogOverviewComponent} from '../createshop/createshop.component';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-verifiedshops',
  templateUrl: './verifiedshops.component.html',
  styleUrls: ['./verifiedshops.component.scss']
})
export class VerifiedshopsComponent {

    status: any;
    shopsList: Array<any>;
    shopList: Array<any>;
    url = '/api/';
    dataSource: MatTableDataSource<any>;

    @ViewChild(MatSort) sort: MatSort;
    displayedColumns = ['name', 'phoneNumber', 'category', 'description', 'status'];
    applyFilter(filterValue: string) {
        filterValue = filterValue.trim(); // Remove whitespace
        filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
        this.dataSource.filter = filterValue;
    }

    constructor(private userService: UserService, private router: Router, public dialog: MatDialog, private spinner: NgxSpinnerService) {
        this.spinner.show();
        this.getShops();
    }

    public getShops() {
        const data = {
            status : ['Y', 'N']
        }
        this.userService.readshops('/api/',data).subscribe(data => {
            this.shopsList = data.data;
            this.dataSource = new MatTableDataSource(this.shopsList);
            this.dataSource.sort = this.sort;
            this.spinner.hide();
        });
    }
    public deactivateShop(id) {
        const dialogRef = this.dialog.open(CnfDeactDialogComponent, {
            width: '450px',
            data: {
                shop_id: id
            }
        });

        dialogRef.afterClosed().subscribe(result => {
            // console.log('The dialog was closed');
            // this.cstatus = result;
        });
    }

    public activateShop(id) {
        const dialogRef = this.dialog.open(CnfActDialogComponent, {
            width: '450px',
            data: {
                shop_id: id
            }
        });

        dialogRef.afterClosed().subscribe(result => {
            // console.log('The dialog was closed');
            // this.cstatus = result;
        });
    }

}

@Component({
    // selector: 'app-cnfActdDctDialog',
    templateUrl: './dshoplist-cnfdact-dialog-component.html',
    styleUrls: ['./dshoplist-cnfdact-dialog-component.scss']
})
export class CnfDeactDialogComponent {
    cstatus: any;
    url; '/api/';
    constructor(
        public dialogRef: MatDialogRef<CnfDeactDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

    onNoClick(): void {
        this.dialogRef.close();
    }

    onDeacativateClick(shopid): void {
        console.log(shopid);
        const shop_data = {
            shop_id: shopid,
            change_status: 'N',
            db_query: 'change_status'
        };
        this.userService.editshopbyid(this.url, shop_data)
            .subscribe(
                data => {
                    if (data.status === true) {
                        this.cstatus = 'Shop De-Activated Successfully !';
                    } else {
                        this.cstatus = 'Unable to De-Activate Please try again !';
                    }
                    this.dialog.open(DialogOverviewComponent, {
                        width: '450px',
                        data: { status: this.cstatus }
                    });
                }

            );
        this.dialogRef.close();
    }

}

@Component({
   // selector: 'app-cnfActdDctDialog',
    templateUrl: './dshoplist-cnfact-dialog-component.html',
    styleUrls: ['./dshoplist-cnfact-dialog-component.scss']
})
export class CnfActDialogComponent {
    cstatus: any;
    url; '/api/';
    constructor(
        public dialogRef: MatDialogRef<CnfActDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

    onNoClick(): void {
        this.dialogRef.close();
    }

    onactivateClick(shopid): void {
        const del_shop = {
            shop_id: shopid,
            change_status: 'Y',
            db_query: 'change_status'
        };
        this.userService.editshopbyid(this.url, del_shop)
            .subscribe(
                data => {
                    if (data.status === true) {
                        this.cstatus = 'Shop Activated Successfully !';
                    } else {
                        this.cstatus = 'Unable to Activate Please try again !';
                    }
                    this.dialog.open(DialogOverviewComponent, {
                        width: '450px',
                        data: { status: this.cstatus }
                    });
                }

            );
        this.dialogRef.close();
    }

}



