import { Component, Inject, ViewChild , OnInit} from '@angular/core';
import { UserService } from '../user.service';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import { MatTableDataSource, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { Observable } from 'rxjs/Observable';
import {FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import {DialogOverviewComponent} from '../createshop/createshop.component';
import {ShoplistDialogComponent} from '../shopslist/shopslist.component';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-dpendingapprovals',
  templateUrl: './dpendingapprovals.component.html',
  styleUrls: ['./dpendingapprovals.component.scss']
})
export class DpendingapprovalsComponent implements OnInit{

    status: any;
    shopsList: Array<any>;
    shopList: Array<any>;
    url = '/api/';
    dataSource: MatTableDataSource<any>;

    @ViewChild(MatSort) sort: MatSort;
    displayedColumns = ['name', 'phoneNumber', 'category', 'description', 'action'];
    applyFilter(filterValue: string) {
        filterValue = filterValue.trim(); // Remove whitespace
        filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
        this.dataSource.filter = filterValue;
    }

    constructor(private userService: UserService, private router: Router, public dialog: MatDialog, private spinner: NgxSpinnerService) {
        this.spinner.show();
        this.getShops();
    }

    ngOnInit(){
        //Close the dropdown if the user clicks outside of it
        window.onclick = function(event) {
            //console.log(event);
            if (!(event.target as HTMLInputElement).matches('.dropbtn')) {
                var dropdowns = document.getElementsByClassName("dropdown-content");
                var i;
                for (i = 0; i < dropdowns.length; i++) {
                    var openDropdown = dropdowns[i];
                    if (openDropdown.classList.contains('show')) {
                        openDropdown.classList.remove('show');
                    }
                }
            }

            
        }
    }

    showDropdown(id) {
        var dropdowns = document.getElementsByClassName("dropdown-content");
                
                for(var j = 0; j < dropdowns.length; j++){
                    var openDropdown = dropdowns[j];
                    if (openDropdown.classList.contains('show')) {
                        openDropdown.classList.remove('show');
                    }
                    
                }
        $(".myDropdown"+id).addClass('show');
    }

    public getShops() {
        const data = {
            status : ['W']
        }
        this.userService.readshops('/api/', data).subscribe(data => {
            this.shopsList = data.data;
            this.dataSource = new MatTableDataSource(this.shopsList);
            this.dataSource.sort = this.sort;
            this.spinner.hide();
        });
    }
    public viewData(id) {
        const shopid = {
            shop_id: id
        };
        this.spinner.show();
        this.userService.readshopbyid(this.url, shopid).subscribe(data => {
            this.shopList = data.data;
            console.log(this.shopList);
            this.spinner.hide();
            this.openDialog();
        });
    }
    public approveData(id) {
        const dialogRef = this.dialog.open(CnfApprDialogComponent, {
            width: '450px',
            data: {shopid: id}
        });

        dialogRef.afterClosed().subscribe(result => {
            // console.log('The dialog was closed');
            // this.cstatus = result;
        });
    }
    public rejectData(id) {
        const dialogRef = this.dialog.open(CnfRejDialogComponent, {
            width: '450px',
            data: {shopid: id}
        });

        dialogRef.afterClosed().subscribe(result => {
            // console.log('The dialog was closed');
            // this.cstatus = result;
        });
    }

    openDialog(): void {
        const dialogRef = this.dialog.open(ShoplistDialogComponent, {
            width: '450px',
            data:  this.shopList[0]
        });

        dialogRef.afterClosed().subscribe(result => {
            // console.log('The dialog was closed');
            // this.cstatus = result;
        });
    }

    ConfirmApprDialog(): void {
        const dialogRef = this.dialog.open(CnfApprDialogComponent, {
            width: '450px',
            data:  this.shopList[0]
        });

        dialogRef.afterClosed().subscribe(result => {
            // console.log('The dialog was closed');
            // this.cstatus = result;
        });
    }

}


@Component({
    selector: 'app-cnfAprDialog',
    templateUrl: './dshoplist-cnfapr-dialog-component.html',
    styleUrls: ['./dshoplist-cnfapr-dialog-component.scss']
})
export class CnfApprDialogComponent {
    cstatus: any;
    url; '/api/';
    constructor(
        public dialogRef: MatDialogRef<CnfApprDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

    onNoClick(): void {
        this.dialogRef.close();
    }

    onApproveClick(shopid): void {
        console.log(shopid)
        const del_shop = {
            shop_id: shopid,
            change_status: 'Y',
            db_query: 'change_status'
        };
        this.userService.editshopbyid(this.url, del_shop)
            .subscribe(
                data => {
                    if (data.status === true) {
                        this.cstatus = 'Shop Approved Successfully !';
                    } else {
                        this.cstatus = 'Unable to approve Please try again !';
                    }
                    this.dialog.open(DialogOverviewComponent, {
                        width: '450px',
                        data: { status: this.cstatus }
                    });
                }

            );
        this.dialogRef.close();
    }

}

@Component({
    selector: 'app-cnfAprDialog',
    templateUrl: './dshoplist-cnfrej-dialog-component.html',
    styleUrls: ['./dshoplist-cnfrej-dialog-component.scss']
})
export class CnfRejDialogComponent {
    cstatus: any;
    url; '/api/';
    constructor(
        public dialogRef: MatDialogRef<CnfRejDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

    onNoClick(): void {
        this.dialogRef.close();
    }

    onRejectClick(shopid): void {
        console.log(shopid);
        const del_shop = {
            shop_id: shopid,
            change_status: 'R',
            db_query: 'change_status'
        };
        this.userService.editshopbyid(this.url, del_shop)
            .subscribe(
                data => {
                    if (data.status === true) {
                        this.cstatus = 'Shop Rejected Successfully !';
                    } else {
                        this.cstatus = 'Unable to Reject Please try again !';
                    }
                    this.dialog.open(DialogOverviewComponent, {
                        width: '450px',
                        data: { status: this.cstatus }
                    });
                }

            );
        this.dialogRef.close();
    }

}





