import { Injectable, EventEmitter, Output, Component, Input } from '@angular/core';
@Injectable()
export class couponSideBarService {

  isCouponOpen = false;

  @Output() couponchange: EventEmitter<boolean> = new EventEmitter();

  toggle() {
    this.isCouponOpen = !this.isCouponOpen;
    this.couponchange.emit(this.isCouponOpen);
  }

  couponSidebarClose() {
    this.couponchange.emit(false);
  }

  couponSidebarOpen() {
    this.couponchange.emit(true);
  }

}


