import { Component, HostBinding, OnInit, Input } from '@angular/core';
import {AbstractControl, FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import { categorySideBarService } from './category-sidebar.service';
import { UserService } from '../user.service';

@Component({
  selector: 'app-category-sidebar',
  templateUrl: './category-sidebar.component.html',
  styleUrls: ['./category-sidebar.component.scss']
})
export class CategorySidebarComponent implements OnInit {
  @HostBinding('class.is-open')
  isOpen = false;
  url = '/api/';
  cstatus: string;

  constructor(private categorysidebar: categorySideBarService,private userService: UserService) { }

  // For Create Category
  createCategoryForm = new FormGroup({
    category: new FormControl('', Validators.compose([Validators.minLength(3),this.noWhitespaceValidator]))
});

public noWhitespaceValidator(control: FormControl) {
  let isWhitespace = (control.value || '').trim().length < 3;
  let isValid = !isWhitespace;
  return isValid ? null : { 'whitespace': true };
}

  ngOnInit() {
    this.categorysidebar.change.subscribe(isOpen => {
      this.isOpen = isOpen;
    });
  }

  // close create category menu
  categoryclose(){
    this.categorysidebar.categorySidebarClose();
  }


   // Function For Create category
   onCreateCategorySubmit(createCategoryForm,formDirective) {
    // const categorydata = {
    //     created_by: userdata.data[0].id,
    //     name: this.createCategoryForm.get('category').value,
    //     status: 'Y'
    // };
    let resdata = {
      text: this.createCategoryForm.get('category').value
    };
    this.userService.createCategory(this.url, resdata)
        .subscribe(
            (data) => {
              console.log(data);
                if (data) {
                    this.cstatus = 'Category Added Successfully !';
                    formDirective.resetForm();
                    createCategoryForm.reset();
                } else {
                    this.cstatus = '<span>Category Already exists !</span>';
                }
                
            },
            (err) => {
              console.log(err);
              this.cstatus = '<span>'+err.error.message+'</span>';
            }

        );
}

}



