import {Component, Inject, OnInit, ViewChild} from '@angular/core';
import { UserService } from '../user.service';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import { MatTableDataSource, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { Observable } from 'rxjs/Observable';
import {FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import {DialogOverviewComponent} from '../createshop/createshop.component';
import { NgxSpinnerService } from 'ngx-spinner';
import * as $ from 'jquery';

const valnumber = /^[0-9][0-9]*$/;

@Component({
  selector: 'app-shopslist',
  templateUrl: './shopslist.component.html',
    styleUrls: ['./shopslist.component.scss']
})

export class ShopslistComponent implements OnInit {
    status: any;
    shopsList: Array<any>;
    shopList: Array<any>;
    url = '/api/';
    dataSource: MatTableDataSource<any>;

    @ViewChild(MatSort) sort: MatSort;
    displayedColumns = ['name', 'phoneNumber', 'category', 'description', 'action'];
    applyFilter(filterValue: string) {
        filterValue = filterValue.trim(); // Remove whitespace
        filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
        this.dataSource.filter = filterValue;
    }

    constructor(private userService: UserService, private router: Router, public dialog: MatDialog, private spinner: NgxSpinnerService) {
        this.spinner.show();
        this.getShops();
    }

    ngOnInit(){
        

        //Close the dropdown if the user clicks outside of it
        window.onclick = function(event) {
            //console.log(event);
            if (!(event.target as HTMLInputElement).matches('.dropbtn')) {
                var dropdowns = document.getElementsByClassName("dropdown-content");
                var i;
                for (i = 0; i < dropdowns.length; i++) {
                    var openDropdown = dropdowns[i];
                    if (openDropdown.classList.contains('show')) {
                        openDropdown.classList.remove('show');
                    }
                }
            }

            
        }
    }

    showDropdown(id) {
        var dropdowns = document.getElementsByClassName("dropdown-content");
                
                for(var j = 0; j < dropdowns.length; j++){
                    var openDropdown = dropdowns[j];
                    if (openDropdown.classList.contains('show')) {
                        openDropdown.classList.remove('show');
                    }
                    
                }
        //$(".myDropdown"+id).toggle();
        $(".myDropdown"+id).addClass('show');
    }

    public getShops() {
        const data = {
            status : ['W']
        }
    this.userService.readshops('/api/',data).subscribe(data => {
        this.shopsList = data.data;
        this.dataSource = new MatTableDataSource(this.shopsList);
        this.dataSource.sort = this.sort;
        this.spinner.hide();
    });
    }
    public viewData(id) {
        const shopid = {
            shop_id: id
        };
        this.spinner.show();

        this.userService.readshopbyid(this.url, shopid).subscribe(data => {
            this.shopList = data.data;
            this.spinner.hide();
            this.openDialog();
        });
    }

    public editData(id) {
        const shopid = {
            shop_id: id
        };
        this.spinner.show();
        this.userService.readshopbyid(this.url, shopid).subscribe(data => {
            this.shopList = data.data;
            this.spinner.hide();
            this.editshopDialog();
        });
    }

    public deleteData(id) {
        const shopid = {
            shop_id: id
        };
        const dialogRef = this.dialog.open(ShoplistDialogComponentConfirm, {
            width: '450px',
             data:  shopid,
        });

        dialogRef.afterClosed().subscribe(result => {
            // console.log('The dialog was closed');
            // this.cstatus = result;
        });
    }

    // Function to edit shop data
    editshopDialog(): void {
        const dialogRef = this.dialog.open(EditshopDialogComponent, {
            width: '450px',
            data:  this.shopList[0]
        });

        dialogRef.afterClosed().subscribe(result => {
            // console.log('The dialog was closed');
            // this.cstatus = result;
        });
    }

    openDialog(): void {
        const dialogRef = this.dialog.open(ShoplistDialogComponent, {
            width: '450px',
            data:  this.shopList[0]
        });

        dialogRef.afterClosed().subscribe(result => {
            // this.cstatus = result;
        });
    }
}

@Component({
    selector: 'app-dialog',
    templateUrl: 'shoplist-dialog-component.html',
    styleUrls: ['./shoplist-dialog-component.scss']
})
export class ShoplistDialogComponent {

    constructor(
        public dialogRef: MatDialogRef<ShoplistDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any) { }

    onNoClick(): void {
        this.dialogRef.close();
    }

}

@Component({
    selector: 'app-dialog-confirm',
    templateUrl: 'shoplist-dialog-component-confirm.html',
    styleUrls: ['./shoplist-dialog-component-confirm.scss']
})
export class ShoplistDialogComponentConfirm {
    url = '/api/';
    cstatus: any;

    constructor(
        public dialogRef: MatDialogRef<ShoplistDialogComponentConfirm>,
        @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

    onNoClick(): void {
        this.dialogRef.close();
    }

    onDeleteClick(shopid): void {
        const del_shop = {
            shop_id: shopid,
            change_status: 'R',
            db_query: 'change_status'
        };
        this.userService.editshopbyid(this.url, del_shop)
            .subscribe(
                data => {
                    console.log(data);
                    if (data.status === true) {
                        this.cstatus = 'Shop Deleted Successfully !';
                    } else {
                        this.cstatus = 'Unable to update Please try again !';
                    }
                    this.dialog.open(DialogOverviewComponent, {
                        width: '450px',
                        data: { status: this.cstatus }
                    });
                }

            );
        this.dialogRef.close();
    }

}

@Component({
    selector: 'app-dialog',
    templateUrl: 'editshop-dialog-component.html',
    styleUrls: ['./editshop-dialog-component.scss']
})
export class EditshopDialogComponent implements OnInit {
    cstatus: any;
    url = '/api/';
    shopList: Array<any>;

    @ViewChild(FormGroupDirective) myform;
    // For Edit shop
    editShopForm = new FormGroup({
        userId: new FormControl(),
        isFavourite: new FormControl(),
        branchID: new FormControl(),
        createdAt: new FormControl(),
        updatedAt: new FormControl(),
        status: new FormControl(),
        shopName: new FormControl('', Validators.compose([Validators.minLength(3)])),
        category: new FormControl('', Validators.compose([Validators.minLength(3)])),
        description: new FormControl('', Validators.compose([Validators.minLength(3)])),
        phoneNumber: new FormControl('', Validators.compose([Validators.maxLength(12),
            Validators.minLength(8), Validators.pattern(valnumber)])),
        timeFrom: new FormControl('', Validators.compose([Validators.minLength(3)])),
        timeTo: new FormControl('', Validators.compose([Validators.minLength(3)])),
        address: new FormControl('', Validators.compose([Validators.minLength(3)])),
        imageUrl: new FormControl('', Validators.compose([Validators.minLength(3)])),
        location: new FormControl('', Validators.compose([Validators.minLength(3)])),
        shopOwner: new FormControl('', Validators.compose([Validators.minLength(3)])),
        shopid: new FormControl()
    });

    ngOnInit() {

    }

    openDialog(): void {
        const dialogRef = this.dialog.open(ShoplistDialogComponent, {
            width: '350px',
            data:  this.shopList[0]
        });
        // this.editShopForm.setValue(this.dialogRef.componentInstance.data);
        dialogRef.afterClosed().subscribe(result => {
            // console.log('The dialog was closed');
            // this.cstatus = result;
        });
    }


    // Function For Edit shop
    onEditShopSubmit() {
        const userdata = JSON.parse(localStorage.getItem('userDetails'));
        const shopdata = {
            user_id: userdata.data[0].id,
            name: this.editShopForm.get('shopName').value,
            category: this.editShopForm.get('category').value,
            description: this.editShopForm.get('description').value,
            phone_number: this.editShopForm.get('phoneNumber').value,
            time_from: this.editShopForm.get('timeFrom').value,
            time_to: this.editShopForm.get('timeTo').value,
            address: this.editShopForm.get('address').value,
            image_url: this.editShopForm.get('imageUrl').value,
            location: this.editShopForm.get('location').value,
            branch_id: '',
            shop_owner: this.editShopForm.get('shopOwner').value,
            db_query: 'update',
            shop_id: this.editShopForm.get('shopid').value
        };
        this.userService.editshopbyid(this.url, shopdata)
            .subscribe(
                data => {
                    if (data.status === true) {
                        this.cstatus = 'Shop Updated Successfully !';
                    } else {
                        this.cstatus = 'Unable to update Please try again !';
                    }
                     this.updateDialog();
                }

            );
    }

    updateDialog(): void {
        const dialogRef = this.dialog.open(DialogOverviewComponent, {
            width: '450px',
            data: { status: this.cstatus }
        });

        dialogRef.afterClosed().subscribe(result => {
            // this.cstatus = result;
        });
    }

    constructor(
        public dialogRef: MatDialogRef<EditshopDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

    onNoClick(): void {
        this.dialogRef.close();
    }

}



