import { Component, OnInit, Inject, ViewChild } from '@angular/core';
 import { UserService } from '../user.service';
import { AbstractControl, FormControl, Validators, FormGroup, FormGroupDirective } from '@angular/forms';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import { NavService } from '../helper/navigationService';
import { MatTableDataSource, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {DialogOverviewComponent} from '../createshop/createshop.component';

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

function passwordConfirming(c: AbstractControl): any {
    if(!c.parent || !c) return;
    const pwd = c.parent.get('rpassword');
    const cpwd= c.parent.get('confirm_password');

    if(!pwd || !cpwd) return ;
    if (pwd.value !== cpwd.value) {
        return { invalid: true };

}
}

@Component({
  selector: 'my-page-login',
  styleUrls: ['./login.component.scss'],
  templateUrl: './login.component.html'
})

export class PageLoginComponent implements OnInit {

    status: any;
    fpstatus: any;
    statusr: any;
    url = '/api/';
    loginDiv = false;
    forgotPwdDiv = true;

    @ViewChild(FormGroupDirective) myform;

    // For Login
    public loginForm = new FormGroup({
        email: new FormControl('', Validators.compose([Validators.pattern(EMAIL_REGEX)])),
        password: new FormControl('', Validators.compose([Validators.minLength(3)]))
    });

    // For Forgot password
    public forgotPasswordForm = new FormGroup({
        femail: new FormControl('', Validators.compose([Validators.pattern(EMAIL_REGEX)])),
    });

    // For Register
    public registerForm = new FormGroup({
        first_name: new FormControl('', Validators.compose([Validators.maxLength(30), Validators.minLength(3)])),
        last_name: new FormControl('', Validators.compose([Validators.maxLength(30), Validators.minLength(3)])),
        email: new FormControl('', Validators.compose([Validators.pattern(EMAIL_REGEX)])),
        mobile: new FormControl('', Validators.compose([Validators.maxLength(12), Validators.minLength(8)])),
        // rpassword: new FormControl('', Validators.compose([Validators.maxLength(20), Validators.minLength(4)])),
        //confirm_password: new FormControl('', Validators.compose([passwordConfirming])),
        location: new FormControl('', Validators.compose([Validators.maxLength(30), Validators.minLength(3)]))
    });


  // constructor(private userService: UserService, private router: Router, private navservice: NavService) {
  constructor(private userService: UserService, private router: Router, private navservice: NavService, 
    public dialog: MatDialog, private formDirective: FormGroupDirective) {
    this.validatepathh();
    this.navservice.setStaus(false);
  }

    User = { };

    public forgotPassword(id) {
        // this.userService.readshopbyid(this.url, shopid).subscribe(data => {
            // this.shopList = data.data;
             this.openDialog();
        // });
    }

    openDialog(): void {
        const dialogRef = this.dialog.open(ForgotpasswordDialogComponent, {
            width: '450px',
            // data:  this.shopList[0]
        });

        dialogRef.afterClosed().subscribe(result => {
            // this.cstatus = result;
        });
    }
    ngOnInit() {
    }

  // Function For login
    onLoginSubmit() {
        const logindata = {
            username: this.loginForm.get('email').value,
            password: this.loginForm.get('password').value
        };

        this.userService.login(this.url, logindata)
            .subscribe(
                data => {
                    console.log(data);
                    this.status = 'Login Success';
                    localStorage.setItem('userDetails', JSON.stringify(data));
                    localStorage.setItem('useremail', this.loginForm.get('email').value);
                    localStorage.setItem('password',this.loginForm.get('password').value);
                    this.router.navigate(['dashboard']);
                },
                err => {
                    console.log(err.error.message);
                    this.status = err.error.message;
                }

            );
    }

    showForgotForm(){
        this.loginDiv = true;
        this.forgotPwdDiv = false;
    }

    showLoginForm(){
        this.loginDiv = false;
        this.forgotPwdDiv = true;
    }

    onForgotSubmit(){
        const forgotpassworddata = {
            username: this.forgotPasswordForm.get('femail').value
        };

        this.userService.forgotpassword(this.url, forgotpassworddata)
            .subscribe(
                (data) => {
                    this.fpstatus = data.message;
                },
                (err) => {
                    this.fpstatus = err.error.message;
                }

            );
    }

    // Function for Register
    onRegisterSubmit(registerForm,formDirective) {
        const regdata = {
            firstName: this.registerForm.get('first_name').value,
            lastName: this.registerForm.get('last_name').value,
            email: this.registerForm.get('email').value,
            phoneNumber: this.registerForm.get('mobile').value,
            // password: this.registerForm.get('rpassword').value,
            address: this.registerForm.get('location').value,
            type: 'agent'
        };
        this.userService.register(this.url, regdata)
            .subscribe(
                (data) => {
                    this.statusr = data.message;
                    formDirective.resetForm();
                    registerForm.reset();
                },
                (err) => {
                    this.statusr = err.error.message;
                }
            );

    }


    // verify whether user is logged in or not
  validatepathh() {
    let path = window.location.pathname;
    let user = localStorage.getItem("userDetails");

    if ((path === '/') && (user != null)) {
        this.router.navigate(['dashboard']);
    }
  }
}

@Component({
    selector: 'app-dialog',
    templateUrl: 'forgotpassword-dialog-component.html',
    styleUrls: ['./forgotpassword-dialog-component.scss']
})
export class ForgotpasswordDialogComponent {
    url = '/api/';
    cstatus: any;
    // For Forgot password
    public forgotPasswordForm = new FormGroup({
        email: new FormControl('', Validators.compose([Validators.pattern(EMAIL_REGEX)]))
    });

    constructor(private userService: UserService,public dialog: MatDialog,
        public dialogRef: MatDialogRef<ForgotpasswordDialogComponent>,
           @Inject(MAT_DIALOG_DATA) public data: any) { }

    onNoClick(): void {
        this.dialogRef.close();
    }

    // Function For Forgot password
    onForgotPasswordSubmit() {
        console.log(this.forgotPasswordForm);
        const forgotpassworddata = {
            username: this.forgotPasswordForm.get('email').value
        };

        this.userService.forgotpassword(this.url, forgotpassworddata)
            .subscribe(
                (data) => {

                    console.log(data);
                    this.onNoClick();
                    this.cstatus = data.message;
                    this.updateDialog();
                },
                (err) => {
                    this.onNoClick();
                    console.log(err);
                    this.cstatus = err.error.message;
                    this.updateDialog();
                }

            );
    }

    updateDialog(): void {
        const dialogRef = this.dialog.open(DialogOverviewComponent, {
            width: '450px',
            data: { status: this.cstatus }
        });

        dialogRef.afterClosed().subscribe(result => {
            // this.cstatus = result;
        });
    }

}



