import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { UserService } from '../user.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import {FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import { MatTableDataSource, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatTabChangeEvent} from '@angular/material';
import {DialogOverviewComponent} from '../createshop/createshop.component';
import * as $ from 'jquery';

@Component({
  selector: 'app-discounts',
  templateUrl: './discounts.component.html',
  styleUrls: ['./discounts.component.scss']
})
export class DiscountsComponent implements OnInit {
  status: any;
  discountsList: Array<any>;
  discountList: Array<any>;
  url = '/api/';

  constructor(private userService: UserService, private spinner: NgxSpinnerService, private router: Router, 
    public dialog: MatDialog) {
      this.spinner.show();
    this.discountsdata();
    }

    ngOnInit() {
      $('.mat-tab-label-container').css('background','#fff');
  
      //Close the dropdown if the user clicks outside of it
      window.onclick = function(event) {
        if (!(event.target as HTMLInputElement).matches('.dropbtn')) {
            var dropdowns = document.getElementsByClassName("dropdown-content");
            var i;
            for (i = 0; i < dropdowns.length; i++) {
                var openDropdown = dropdowns[i];
                if (openDropdown.classList.contains('show')) {
                    openDropdown.classList.remove('show');
                }
            }
        }
  
        
      }
    }

    onTabClick (event: MatTabChangeEvent){
      var index = event.index;
  
      //this.discountsdata(status);
  }
  showDropdown(id) {
    var dropdowns = document.getElementsByClassName("dropdown-content");
            
            for(var j = 0; j < dropdowns.length; j++){
                var openDropdown = dropdowns[j];
                if (openDropdown.classList.contains('show')) {
                    openDropdown.classList.remove('show');
                }
                
            }
    //$(".myDropdown"+id).toggle();
    $(".myDropdown"+id).addClass('show');
    }

    discountsdata (){
      this.spinner.show();
    this.userService.readdiscounts('/api/').subscribe(data => {
        this.discountsList = data;
        
        this.spinner.hide();
    });
  }

}



