import { Component, OnInit, Inject, ViewChild, ChangeDetectorRef } from '@angular/core';
import { UserService } from '../user.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import {AbstractControl, FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import { MatTableDataSource, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatTabChangeEvent} from '@angular/material';
import {DialogOverviewComponent} from '../createshop/createshop.component';
import * as $ from 'jquery';
import { subcategorySideBarService } from '../subcategory-sidebar/subcategory-sidebar.service';
import { categorySideBarService } from '../category-sidebar/category-sidebar.service';


@Component({
  selector: 'app-subcategories',
  templateUrl: './subcategories.component.html',
  styleUrls: ['./subcategories.component.scss']
})
export class SubcategoriesComponent implements OnInit {
  status: any;
  subcategoriesList: Array<any>;
  subcategoryList: Array<any>;
  categoriesList: Array<any>;
  activeSubCategoriesList: Array<any>;
  deactiveSubCategoriesList: Array<any>;
  message: any;
  url = '/api/';
  cstatus: string;
  breakpoint: number;

  @ViewChild(FormGroupDirective) myform;

    // For Create sub Category
    createSubCategoryForm = new FormGroup({
        subcategory: new FormControl('', Validators.compose([Validators.minLength(3),this.noWhitespaceValidator])),
        category:new FormControl('', Validators.compose([Validators.minLength(1)])),
    });

    public noWhitespaceValidator(control: FormControl) {
        let isWhitespace = (control.value || '').trim().length < 3;
        let isValid = !isWhitespace;
        return isValid ? null : { 'whitespace': true };
    }

    constructor(private userService: UserService, private spinner: NgxSpinnerService, private router: Router, 
      public dialog: MatDialog,private formDirective: FormGroupDirective, private cd: ChangeDetectorRef,
      private categorysidebar: categorySideBarService, private subcategorysidebar: subcategorySideBarService) {
        this.spinner.show();
      this.subcategoriesdata('ALL');
      this.categoriesdata('ALL');

      }
      subcategoryopen(){
        this.subcategorysidebar.subcategorySidebarOpen();
      }

      // Function For Create category
    onCreateSubCategorySubmit(createSubCategoryForm,formDirective) {
        const userdata = JSON.parse(localStorage.getItem('userDetails'));
        const subcategorydata = {
            created_by: userdata.data[0].id,
            category_id: this.createSubCategoryForm.get('category').value,
            name: this.createSubCategoryForm.get('subcategory').value,
            status: 'Y'
        };
        this.userService.createSubCategory(this.url, subcategorydata)
            .subscribe(
                data => {
                    if (data.status === true) {
                        this.cstatus = 'Sub Category Added Successfully !';
                        formDirective.resetForm();
                        createSubCategoryForm.reset();
                    } else {
                        this.cstatus = '<span>Sub Category Already exists !</span>';
                    }
                    
                }

            );
    }

      onResize(event) {
        this.breakpoint = (event.target.innerWidth <= 400) ? 12 : 3;
      }
      ngOnInit() {
        this.breakpoint = (window.innerWidth <= 400) ? 12 : 3;
        this.categorysidebar.categorySidebarClose();
        this.subcategorysidebar.subcategorySidebarClose();
        $('.mat-tab-label-container').css('background','#fff');
    
        //Close the dropdown if the user clicks outside of it
        window.onclick = function(event) {
          if (!(event.target as HTMLInputElement).matches('.dropbtn')) {
              var dropdowns = document.getElementsByClassName("dropdown-content");
              var i;
              for (i = 0; i < dropdowns.length; i++) {
                  var openDropdown = dropdowns[i];
                  if (openDropdown.classList.contains('show')) {
                      openDropdown.classList.remove('show');
                  }
              }
          }
    
          
        }
      }

      onTabClick (event: MatTabChangeEvent){
        var index = event.index;
        
    }

    categoriesdata (status){
        this.userService.readCategories('/api/').subscribe(data => {
            this.categoriesList = [];
            for (let i = 0; i < data.length; i++){
                this.categoriesList.push({value: data[i].id, viewValue: data[i].text});
            }
            this.cd.detectChanges();
            
        });
      }

    subcategoriesdata (status){
        this.spinner.show();
      this.userService.readCategories('/api/').subscribe(data => {
        this.subcategoriesList = data.filter(category => category.parentid != null);
        this.activeSubCategoriesList = this.subcategoriesList.filter(category => category.active == true);
          this.deactiveSubCategoriesList = this.subcategoriesList.filter(category => category.active == false);
          
          this.spinner.hide();
      });
    }

    showDropdown(id) {
      var dropdowns = document.getElementsByClassName("dropdown-content");
              
              for(var j = 0; j < dropdowns.length; j++){
                  var openDropdown = dropdowns[j];
                  if (openDropdown.classList.contains('show')) {
                      openDropdown.classList.remove('show');
                  }
                  
              }
      $(".myDropdown"+id).addClass('show');
      }

      public editData(id) {
        // const categoryid = {
        //     subcategory_id: id
        // };
        this.subcategoryList = this.subcategoriesList.find(subCategory => subCategory.id == id);
        this.editcategoryDialog();
    }

    // Function to edit category data
    editcategoryDialog(): void {
        const dialogRef = this.dialog.open(EditsubcategoriesDialogComponent, {
            width: '450px',
            data:  this.subcategoryList
        });

        dialogRef.afterClosed().subscribe(result => {
        });
    }

    // Function to block category
    public blockData(id) {
        let reqdata = {
            id: id,
            active: false
        }
        this.userService.activeDeactiveSubCategorybyid(this.url, reqdata).subscribe(data => {
            this.message = 'Sub Category Blocked !';
            this.modifycategoryDialog();
        });
    }

    // Function to unblock category
    public unblockData(id) {
        let reqdata = {
            id: id,
            active: true
        }
        this.userService.activeDeactiveSubCategorybyid(this.url, reqdata).subscribe(data => {
            this.message = 'Sub Category Activated!';
            this.modifycategoryDialog();
        });
    }

    modifycategoryDialog(): void {
      const dialogRef = this.dialog.open(SubCategorieslistActDactDialogComponent, {
          width: '450px',
          data:  this.message
      });

      dialogRef.afterClosed().subscribe(result => {
      });
  }



}

@Component({
  selector: 'app-dialog',
  templateUrl: 'editsubcategory-dialog-component.html',
  styleUrls: ['./editsubcategory-dialog-component.scss']
})
export class EditsubcategoriesDialogComponent implements OnInit {
  cstatus: any;
  url = '/api/';
  subcategoryList: Array<any>;

  @ViewChild(FormGroupDirective) myform;
  // For Edit shop
  editSubCategoryForm = new FormGroup({
      status: new FormControl(),
      subcategory: new FormControl('', Validators.compose([Validators.minLength(3)])),
      subcategoryid: new FormControl()
  });

  ngOnInit() {

  }


  // Function For Edit category
  onEditSubCategorySubmit() {

    let reqdata = {
        id: this.editSubCategoryForm.get('subcategoryid').value,
        text: this.editSubCategoryForm.get('subcategory').value
    }
      this.userService.editSubCategorybyid(this.url, reqdata)
          .subscribe(
              (data) => {  
                this.cstatus = 'Sub Category Updated Successfully !';
                this.updateDialog();
              },
              (err) => {
                  this.cstatus = err.error.message;
                  this.updateDialog();
              }

          );
  }

  updateDialog(): void {
      const dialogRef = this.dialog.open(DialogOverviewComponent, {
          width: '450px',
          data: { status: this.cstatus }
      });

      dialogRef.afterClosed().subscribe(result => {
          // this.cstatus = result;
      });
  }

  constructor(
      public dialogRef: MatDialogRef<EditsubcategoriesDialogComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

  onNoClick(): void {
      this.dialogRef.close();
  }

}

@Component({
  selector: 'app-dialog',
  templateUrl: 'subcategorylist-dialog-component.html',
  styleUrls: ['./subcategorylist-dialog-component.scss']
})
export class SubCategorieslistDialogComponent {

  constructor(
      public dialogRef: MatDialogRef<SubCategorieslistDialogComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any) { }

  onNoClick(): void {
      this.dialogRef.close();
  }

}

@Component({
  selector: 'app-dialog',
  templateUrl: 'subcategorylistActDact-dialog-component.html',
  styleUrls: ['./subcategorylistActDact-dialog-component.scss']
})
export class SubCategorieslistActDactDialogComponent {

  constructor(
      public dialogRef: MatDialogRef<SubCategorieslistActDactDialogComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any) { }

  onNoClick(): void {
      this.dialogRef.close();
      location.reload();
  }

}




