import { Component, HostBinding, OnInit, ChangeDetectorRef, Input } from '@angular/core';
import {AbstractControl, FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import { couponSideBarService } from './coupons-sidebar.service';
import { UserService } from '../user.service';

@Component({
  selector: 'app-coupon-sidebar',
  templateUrl: './coupon-sidebar.component.html',
  styleUrls: ['./coupon-sidebar.component.scss']
})
export class CouponSidebarComponent implements OnInit {
  @HostBinding('class.is-couponOpen')
  isCouponOpen = false;
  url = '/api/';
  cstatus: string;
  activeShopsList: Array<any>;
  shopsList: Array<any>;

  constructor(private userService: UserService, private cd: ChangeDetectorRef, private couponSidebar: couponSideBarService) { }

  // For Create Category
  createCouponForm = new FormGroup({
     description: new FormControl('', Validators.compose([Validators.minLength(3)])),
     conditions: new FormControl('', Validators.compose([Validators.minLength(3)])),
     validFrom: new FormControl('',Validators.compose([Validators.required,this.noWhitespaceValidator])),
     validTo: new FormControl('',Validators.compose([Validators.required,this.noWhitespaceValidator])),
     code: new FormControl('',Validators.compose([Validators.required,this.noWhitespaceValidator])),
     offPercentage: new FormControl('',Validators.compose([Validators.required,this.noWhitespaceValidator])),
     shopid: new FormControl('',Validators.compose([Validators.required]))
});

public noWhitespaceValidator(control: FormControl) {
  let isWhitespace = (control.value || '').trim().length < 1;
  let isValid = !isWhitespace;
  return isValid ? null : { 'whitespace': true };
}

ngOnInit() {
  this.couponSidebar.couponchange.subscribe(CouponOpen => {
    this.isCouponOpen = CouponOpen;
    this.shopsdata();
  });
}



// Function to read shops
shopsdata (){

  this.userService.readshops('/api/','').subscribe(data => {
      this.activeShopsList = data.filter(shops => shops.active == true);
      console.log(this.activeShopsList);

      this.shopsList = [];
      for (let i = 0; i < this.activeShopsList.length; i++){
        this.shopsList.push({value: this.activeShopsList[i].id, viewValue: this.activeShopsList[i].name});
      }
      this.cd.detectChanges();
      
  });
  }

  couponclose(){
    this.couponSidebar.couponSidebarClose();
  }

  // Function For Create coupon
  onCreateCouponSubmit(createCouponForm,formDirective) {
    let userdetails = {
      email: localStorage.getItem('useremail'),
      password: localStorage.getItem('password')
  }

  let actShop = this.activeShopsList.find(shop => shop.id == this.createCouponForm.get('shopid').value);
  let branchId = actShop.branchId;
    
    let reqdata = {
       description: this.createCouponForm.get('description').value,
       conditions: this.createCouponForm.get('conditions').value,
       validFrom: this.createCouponForm.get('validFrom').value,
       validTo: this.createCouponForm.get('validTo').value,
       code: this.createCouponForm.get('code').value,
       offPercentage: parseInt(this.createCouponForm.get('offPercentage').value),
       shopid: parseInt(this.createCouponForm.get('shopid').value),
       shopBranchId: branchId
    }

    console.log(reqdata);
    // this.userService.createDiscount(userdetails, reqdata)
    //     .subscribe(
    //         (data) => {
    //           console.log(data);
    //             if (data) {
    //                 this.cstatus = 'Coupon Added Successfully !';
    //                 formDirective.resetForm();
    //                 createCouponForm.reset();
    //             }
                
    //         },
    //         (err) => {
    //           console.log(err);
    //           this.cstatus = '<span style="color:red">'+err.error.message+'</span>';
    //         }

    //     );
}

}



