import { Component, OnInit, Inject, ViewChild, Pipe, PipeTransform } from '@angular/core';
import { UserService } from '../user.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import {FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import { MatTableDataSource, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatTabChangeEvent} from '@angular/material';
import {DialogOverviewComponent} from '../createshop/createshop.component';
import * as $ from 'jquery';

@Component({
  selector: 'app-subscription-plan',
  templateUrl: './subscription-plan.component.html',
  styleUrls: ['./subscription-plan.component.scss']
})
export class SubscriptionPlanComponent implements OnInit {
  cstatus: any;
  url = '/api/';
  subscriptionPlansList: Array<any>;
  subscriptionPlanList: Array<any>;
  message: any;

  constructor(private userService: UserService, private spinner: NgxSpinnerService, private router: Router, 
    public dialog: MatDialog) { 
    this.spinner.show();
    this.subscriptionPlansdata();
    }

  ngOnInit() {
  }

  subscriptionPlansdata (){
    this.spinner.show();
  this.userService.readsubscriptionPlans('/api/').subscribe(data => {
      this.subscriptionPlansList = data;
      console.log(data);
      this.spinner.hide();
  });
}

showDropdown(id) {
  var dropdowns = document.getElementsByClassName("dropdown-content");
          
          for(var j = 0; j < dropdowns.length; j++){
              var openDropdown = dropdowns[j];
              if (openDropdown.classList.contains('show')) {
                  openDropdown.classList.remove('show');
              }
              
          }
  $(".myDropdown"+id).addClass('show');
  }

  updateSubscriptionPlan(id){
    this.spinner.show();
        let selectedSubscriptionPlan = this.subscriptionPlansList.find(subscriptionPlans => subscriptionPlans.id == id);
        this.subscriptionPlanList = selectedSubscriptionPlan;
        this.spinner.hide();
        this.editsubscriptionplanDialog();
  }

  deleteSubscriptionPlan(id){
    this.spinner.show();
    let reqData = {
        id: id
    }
    this.userService.deleteSubscriptionPlanById(reqData).subscribe(
        (data) => {
        this.message = "Coupon Deleted Successfully!";
        console.log(data);
        this.modifySubscriptionPlanDialog();
        this.spinner.hide();
        },
        (err) => {
            console.log(err);
            this.message = err.error.message;
            this.modifySubscriptionPlanDialog();
            this.spinner.hide();
        }

    );

  }

  modifySubscriptionPlanDialog(): void {
    const dialogRef = this.dialog.open(SubslistActDactDialogComponent, {
        width: '450px',
        data:  this.message
    });

    dialogRef.afterClosed().subscribe(result => {
    });
}

  // Function to edit Subscription data
  editsubscriptionplanDialog(): void {
    const dialogRef = this.dialog.open(EditsubscriptionplansDialogComponent, {
        width: '450px',
        data:  this.subscriptionPlanList
    });
    console.log(this.subscriptionPlanList);

    dialogRef.afterClosed().subscribe(result => {
        // console.log('The dialog was closed');
        // this.cstatus = result;
    });
}


}

@Component({
  selector: 'app-dialog',
  templateUrl: 'editsubscriptionplan-dialog-component.html',
  styleUrls: ['./editsubscriptionplan-dialog-component.scss']
})
export class EditsubscriptionplansDialogComponent implements OnInit {
  cstatus: any;
  url = '/api/';
  subscriptionList: Array<any>;

  @ViewChild(FormGroupDirective) myform;

  editSubscriptionPlanForm = new FormGroup({
    subscriptionplanid: new FormControl(''),
    name: new FormControl('', Validators.compose([Validators.minLength(1)])),
    credits: new FormControl('', Validators.compose([Validators.minLength(1)])),
    validity: new FormControl('', Validators.compose([Validators.minLength(1)])),
    price: new FormControl('', Validators.compose([Validators.minLength(1)])),
    planType: new FormControl('', Validators.compose([Validators.minLength(1)])),
    for: new FormControl('', Validators.compose([Validators.minLength(1)])),

});

onEditSubscriptionplanSubmit(){

  let reqdata = {
      id: this.editSubscriptionPlanForm.get('subscriptionplanid').value,
      credits: this.editSubscriptionPlanForm.get('credits').value,
      name: this.editSubscriptionPlanForm.get('name').value,
      validity: this.editSubscriptionPlanForm.get('validity').value,
      price: this.editSubscriptionPlanForm.get('price').value,
      planType: this.editSubscriptionPlanForm.get('planType').value,
      for: this.editSubscriptionPlanForm.get('for').value,
      
  }
  
  this.userService.editSubscriptionPlanbyid(this.url, reqdata)
      .subscribe(
          (data) => {
            this.cstatus = 'Subscription Updated Successfully !';
            this.updateDialog();
          },
          (err) => {
              console.log(err);
            this.cstatus = err.error.message;
            this.updateDialog();
          }

      );
      
}

updateDialog(): void {
  const dialogRef = this.dialog.open(DialogOverviewComponent, {
      width: '450px',
      data: { status: this.cstatus }
  });

  dialogRef.afterClosed().subscribe(result => {
      // this.cstatus = result;
  });
}

constructor(
  public dialogRef: MatDialogRef<EditsubscriptionplansDialogComponent>,
  @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

onNoClick(): void {
  this.dialogRef.close();
}

  ngOnInit(){

  }


}

@Component({
  selector: 'app-dialog',
  templateUrl: 'subslistActDact-dialog-component.html',
  styleUrls: ['./subslistActDact-dialog-component.scss']
})
export class SubslistActDactDialogComponent {

  constructor(
      public dialogRef: MatDialogRef<SubslistActDactDialogComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any) { }

  onNoClick(): void {
      this.dialogRef.close();
      location.reload();
  }

}



