@Component({
selector: 'app-dialog-confirm',
templateUrl: 'shop-dialog-component-confirm.html',
styleUrls: ['./shop-dialog-component-confirm.scss']
})
export class ShoplistdataDialogComponentConfirm {
url = '/api/';
cstatus: any;

constructor(
    public dialogRef: MatDialogRef<ShoplistdataDialogComponentConfirm>,
    @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

onNoClick(): void {
    this.dialogRef.close();
}

onDeactivateClick(shopid): void {
    let userdetails = {
        email: localStorage.getItem('useremail'),
        password: localStorage.getItem('password')
    }
    let reqdata = {
        active: false,
        id: shopid
    }
    this.userService.activeDeactiveShopById(userdetails, reqdata)
        .subscribe(
            (data) => {
                this.cstatus = 'Shop Deactivated Successfully !';
                this.dialog.open(DialogOverviewComponent, {
                    width: '450px',
                    data: { status: this.cstatus }
                });
            },
            (err) => {
                this.cstatus = err.error.message;
                this.dialog.open(DialogOverviewComponent, {
                    width: '450px',
                    data: { status: this.cstatus }
                });
            }

        );
    this.dialogRef.close();
}

}

@Component({
    selector: 'app-dialog-activate',
    templateUrl: 'shop-dialog-component-activate.html',
    styleUrls: ['./shop-dialog-component-confirm.scss']
    })
    export class ShoplistdataDialogComponentActivate {
    url = '/api/';
    cstatus: any;
    
    constructor(
        public dialogRef: MatDialogRef<ShoplistdataDialogComponentActivate>,
        @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }
    
    onNoClick(): void {
        this.dialogRef.close();
    }
    
    onActivateClick(shopid): void {
        let userdetails = {
            email: localStorage.getItem('useremail'),
            password: localStorage.getItem('password')
        }
        let reqdata = {
            active: true,
            id: shopid
        }

        this.userService.activeDeactiveShopById(userdetails, reqdata)
            .subscribe(
                (data) => {
                    this.cstatus = 'Shop Activated Successfully !';
                    this.dialog.open(DialogOverviewComponent, {
                        width: '450px',
                        data: { status: this.cstatus }
                    });
                },
                (err) => {
                    this.cstatus = err.error.message;
                    this.dialog.open(DialogOverviewComponent, {
                        width: '450px',
                        data: { status: this.cstatus }
                    });
                }
    
            );
        this.dialogRef.close();
    }
    
    }

@Component({
selector: 'app-dialog',
templateUrl: 'editshop-dialog-component.html',
styleUrls: ['./editshop-dialog-component.scss']
})
export class EditshopdataDialogComponent implements OnInit {
cstatus: any;
url = '/api/';
shopList: Array<any>;

@ViewChild(FormGroupDirective) myform;
// For Edit shop
editShopForm = new FormGroup({
    userId: new FormControl(),
    isFavourite: new FormControl(),
    branchID: new FormControl(),
    createdAt: new FormControl(),
    updatedAt: new FormControl(),
    status: new FormControl(),
    shopName: new FormControl('', Validators.compose([Validators.minLength(3), this.noWhitespaceValidator])),
    category: new FormControl('', Validators.compose([Validators.minLength(3), this.noWhitespaceValidator])),
    description: new FormControl('', Validators.compose([Validators.minLength(3)])),
    phoneNumber: new FormControl('', Validators.compose([Validators.maxLength(12),
        Validators.minLength(8), Validators.pattern(valnumber)])),
    timeFrom: new FormControl('', Validators.compose([Validators.minLength(3)])),
    timeTo: new FormControl('', Validators.compose([Validators.minLength(3)])),
    address: new FormControl('', Validators.compose([Validators.minLength(3), this.noWhitespaceValidator])),
    imageUrl: new FormControl('', Validators.compose([Validators.minLength(3)])),
    //location: new FormControl('', Validators.compose([Validators.minLength(3), this.noWhitespaceValidator])),
    shopOwner: new FormControl('', Validators.compose([Validators.minLength(3), this.noWhitespaceValidator])),
    shopid: new FormControl()
});

public noWhitespaceValidator(control: FormControl) {
    let isWhitespace = (control.value || '').trim().length < 3;
    let isValid = !isWhitespace;
    return isValid ? null : { 'whitespace': true };
}

ngOnInit() {

}

openDialog(): void {
    const dialogRef = this.dialog.open(ShoplistDialogComponent, {
        width: '350px',
        data:  this.shopList[0]
    });
    // this.editShopForm.setValue(this.dialogRef.componentInstance.data);
    dialogRef.afterClosed().subscribe(result => {
        // console.log('The dialog was closed');
        // this.cstatus = result;
    });
}


// Function For Edit shop
onEditShopSubmit() {

    let shopOwner = {"ownerEmail": "msrao.dari@gmail.com",
    "ownerLastName": "M",
    "ownerName": ""+this.editShopForm.get('shopOwner').value,
    "ownerPhoneNum": ""+this.editShopForm.get('phoneNumber').value,
    "website": "www.himajainfotech.com "};
    const shopdata = {
        name: this.editShopForm.get('shopName').value,
        category: this.editShopForm.get('category').value,
        description: this.editShopForm.get('description').value,
        phoneNumber: this.editShopForm.get('phoneNumber').value,
        timeFrom: this.editShopForm.get('timeFrom').value,
        timeTo: this.editShopForm.get('timeTo').value,
        address: this.editShopForm.get('address').value,
        //image_url: this.editShopForm.get('imageUrl').value,
        //location: this.editShopForm.get('location').value,
        shopOwner: JSON.stringify(shopOwner),
        id: this.editShopForm.get('shopid').value
    };
    this.userService.editshopbyid(this.url, shopdata)
        .subscribe(
            (data) => {
                this.cstatus = 'Shop Updated Successfully !';
                 this.updateDialog();
            },
            (err) => {
                this.cstatus = err.error.message;
            }

        );
}

updateDialog(): void {
    const dialogRef = this.dialog.open(DialogOverviewComponent, {
        width: '450px',
        data: { status: this.cstatus }
    });

    dialogRef.afterClosed().subscribe(result => {
        // this.cstatus = result;
    });
}

constructor(
    public dialogRef: MatDialogRef<EditshopdataDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

onNoClick(): void {
    this.dialogRef.close();
}
  

}

  



