import { Component, OnInit, ViewChild, Inject, ElementRef, NgZone } from '@angular/core';
import { Location } from '@angular/common';
import { UserService } from '../user.service';
import { AbstractControl, FormControl, Validators, FormGroup, FormGroupDirective } from '@angular/forms';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {DialogOverviewComponent} from '../createshop/createshop.component';
import { MapsAPILoader } from '@agm/core';
import { categorySideBarService } from '../category-sidebar/category-sidebar.service';
import { subcategorySideBarService } from '../subcategory-sidebar/subcategory-sidebar.service';

const valnumber = /^[0-9][0-9]*$/;
const eml = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
declare var google: any

@Component({
  selector: 'app-createagent',
  templateUrl: './createagent.component.html',
  styleUrls: ['./createagent.component.scss']
})
export class CreateagentComponent implements OnInit {
    /* this is for map */
    public latitude: number;
    public longitude: number;
    public searchControl: FormControl;
    public zoom: number;
    @ViewChild("search")
    public searchElementRef: ElementRef;
    private geoCoder;


    url = '/api/';
    cstatus: string;

    @ViewChild(FormGroupDirective) myform;

    // For Create shop
    createAgentForm = new FormGroup({
        firstName: new FormControl('', Validators.compose([Validators.minLength(3)])),
        lastName: new FormControl('', Validators.compose([Validators.minLength(3)])),
        email: new FormControl('', Validators.pattern(eml)),
        phoneNumber: new FormControl('', Validators.compose([Validators.maxLength(10),
            Validators.minLength(10), Validators.pattern(valnumber)])),
        address: new FormControl('', Validators.compose([Validators.minLength(1)])),
        primaryArea: new FormControl('', Validators.compose([Validators.minLength(3)])),
        secondaryArea: new FormControl('', Validators.compose([Validators.minLength(3)])),
        tertiaryArea: new FormControl('', Validators.compose([Validators.minLength(3)])),
        dailyTargets: new FormControl('', Validators.compose([Validators.minLength(1)]))
    });

    constructor(private userService: UserService, public dialog: MatDialog, private mapsAPILoader: MapsAPILoader,
                private ngZone: NgZone, private location: Location, private categorysidebar: categorySideBarService, 
                private subcategorysidebar: subcategorySideBarService) { }

  ngOnInit() {
    this.categorysidebar.categorySidebarClose();
    this.subcategorysidebar.subcategorySidebarClose();

      // set google maps defaults
      this.zoom = 10;
      this.latitude = 17.4252392;
      this.longitude = 78.4252013;
      // create search FormControl
      this.searchControl = new FormControl();

      // set current position
      this.setCurrentPosition();

      // load Places Autocomplete
      this.mapsAPILoader.load().then(() => {
          let autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement, {
              types: ["address"]
          });
          this.geoCoder = new google.maps.Geocoder;
          autocomplete.addListener("place_changed", () => {
              this.ngZone.run(() => {
                  // get the place result
                  let place: any = autocomplete.getPlace();
                  // verify result
                  if (place.geometry === undefined || place.geometry === null) {
                      return;
                  }
                  // set latitude, longitude and zoom
                  this.latitude = place.geometry.location.lat();
                  this.longitude = place.geometry.location.lng();
                  this.zoom = 12;
              });
          });
      });
  }

  goback(){
    this.location.back();
}

    // Function For Create Agent
    onCreateAgentSubmit() {
        const userdata = JSON.parse(localStorage.getItem('userDetails'));
        const agentdata = {
            firstName: this.createAgentForm.get('firstName').value,
            lastName: this.createAgentForm.get('lastName').value,
            email: this.createAgentForm.get('email').value,
            phone: this.createAgentForm.get('phoneNumber').value,
            address: this.searchElementRef.nativeElement.value,
        };
        this.userService.createAgent(this.url, agentdata)
            .subscribe(
                data => {
                    if (data.status === true) {
                        this.cstatus = 'Agent Created Successfully !';
                    } else {
                        this.cstatus = 'Agent Already Registerd !';
                    }
                    this.openDialog();
                }

            );
    }

    openDialog(): void {
        let dialogRef = this.dialog.open(DialogOverviewComponent, {
            width: '450px',
            data: { status: this.cstatus }
        });

        dialogRef.afterClosed().subscribe(result => {
            this.cstatus = result;
        });
    }

    private setCurrentPosition() {
        if ("geolocation" in navigator) {
            navigator.geolocation.getCurrentPosition((position) => {
                this.latitude = position.coords.latitude;
                this.longitude = position.coords.longitude;
                this.zoom = 12;
            });
        }
    }

    public markerEnd(markerDetails: any): void {
        if (markerDetails) {
            this.latitude = markerDetails.coords.lat;
            this.longitude = markerDetails.coords.lng;
            this.geoCoder.geocode({'location': {lat: this.latitude, lng: this.longitude }}, (results, status) => {
                
                if (status === 'OK') {
                    if (results[0]) {
                        this.searchElementRef.nativeElement.value = results[0].formatted_address;
                        // this.searchElementRef.nativeElement.value = results[0].formatted_address);
                        // console.log(this.searchElementRef.nativeElement.value);
                        // infowindow.setContent(results[0].formatted_address);

                    } else {
                        window.alert('No results found');
                    }
                } else {
                    window.alert('Geocoder failed due to: ' + status);
                }

            });
        }
    }

    getLocation() {
        if ("geolocation" in navigator) {
            navigator.geolocation.getCurrentPosition((position) => {
                this.latitude = position.coords.latitude;
                this.longitude = position.coords.longitude;
                
                this.geoCoder.geocode({'location': {lat: this.latitude, lng: this.longitude }}, (results, status) => {
                    
                    if (status === 'OK') {
                        if (results[0]) {
                            this.searchElementRef.nativeElement.value = results[0].formatted_address;
                            
                        } else {
                            window.alert('No results found');
                        }
                    } else {
                        window.alert('Geocoder failed due to: ' + status);
                    }
    
                });
            });
        } else { 
            console.log("Geolocation is not supported by this browser.");
        }
    }

}




