import { Injectable, EventEmitter, Output, Component, Input } from '@angular/core';
@Injectable()
export class discountSideBarService {

  isDiscountOpen = false;

  @Output() discountchange: EventEmitter<boolean> = new EventEmitter();

  toggle() {
    this.isDiscountOpen = !this.isDiscountOpen;
    this.discountchange.emit(this.isDiscountOpen);
  }

  discountSidebarClose() {
    this.discountchange.emit(false);
  }

  discountSidebarOpen() {
    this.discountchange.emit(true);
  }

}


