import { Component, OnInit } from '@angular/core';
// import { CHARTCONFIG } from '../charts/charts.config';
import { NavService } from '../helper/navigationService';
import { UserService } from '../user.service';
import { Chart } from 'angular-highcharts';
import * as moment from 'moment';

@Component({
  selector: 'my-dashboard',
  templateUrl: './dashboard.component.html'
})
export class DashboardComponent {

    public daterange: any = {};
    from_date: any;
    to_date: any;

    public options: any = {
        locale: { format: 'YYYY-MM-DD' },
        alwaysShowCalendars: false,
        startDate: moment().subtract(7, 'day'),
    };

    optiondates = [
        {value: 'lastweek', viewValue: 'Last Week'},
        {value: 'lastmonth', viewValue: 'Last Month'},
        {value: 'lastquarter', viewValue: 'Last Quarter'}
    ];

 shopmetricschart: Chart;
 discountmetricschart: Chart;
 usermetricschart: Chart;
 couponmetricschart: Chart;

 shopsMetrics: Array<any>;
 agentMetrics: Array<any>;
 couponMetrics: Array<any>;
 dashboardmetrics: Array<any>;
 discountMetrics: Array<any>;

 ngOnInit () {
  this.getdashboardmetrics();
  this.getshopMetricsbydate(null, null);
  this.getagentMetricsbydate(null, null);
  this.getcouponMetricsbydate(null, null);
  this.getdiscountMetricsbydate(null, null);
}

    constructor( private navService: NavService, private userService: UserService) {
        this.navService.setStaus(true);
    }


    selectedDate (selected, funct) {
        if (selected === 'lastweek') {
            // from = moment().subtract(7, 'day');
            var from = moment().subtract(14, 'day').format('YYYY-MM-DD');
            var to = moment().subtract(7, 'day').format('YYYY-MM-DD');
        }else if (selected === 'lastmonth') {
            var from = moment().subtract(1, 'months').startOf('month').format('YYYY-MM-DD');
            var to = moment().subtract(1, 'months').endOf('month').format('YYYY-MM-DD');
        }else if (selected === 'lastquarter') {
            var from = moment().subtract(4, 'months').startOf('month').format('YYYY-MM-DD');
            var to = moment().subtract(0, 'day').format('YYYY-MM-DD');
        }else {
            var from = moment().subtract(7, 'day').format('YYYY-MM-DD');
            var to = moment().subtract(0, 'day').format('YYYY-MM-DD');
        }

        if (funct === 'getshopMetrics') {
            this.getshopMetricsbydate(from, to);
        }else if (funct === 'getagentMetrics') {
            this.getagentMetricsbydate(from, to);
        } else if (funct === 'getdiscountMetrics') {
            this.getdiscountMetricsbydate(from, to);
        }else if (funct === 'getcouponMetrics') {
            this.getcouponMetricsbydate(from, to);
        }
    }


  getshopMetricsbydate(from, to) {
      var shopkeypair = [];
      var shopvalues = [];
     if (from == null && to == null) {
         var days = 7; // Days you want to subtract
         var date = new Date();
         var last = new Date(date.getTime() - (days * 24 * 60 * 60 * 1000));
         var day = last.getDate();
         let d = '';
         var zero = 0;
         if(day < 10){
             d = ""+zero+day;
         }else {
             d = ""+day;
         }
         var month = last.getMonth() + 1;
         let m = '';
         if(month < 10){
            m = ""+zero+month;
         }else {
             m = ""+month
         }
         var year = last.getFullYear();
         this.from_date = year+"-"+m+"-"+d;

         let td = '';
         if(date.getDate() < 10){
             td = ""+zero+date.getDate();
         }else {
             td = ""+date.getDate();
         }
         let tm = '';
         if((date.getMonth()+1) < 10){
             tm = ""+zero+(date.getMonth()+1);
         }else {
             tm = ""+(date.getMonth()+1);
         }

         this.to_date = date.getFullYear()+"-"+tm+"-"+td;
     }else {
         this.from_date = from;
         this.to_date = to;
     }
    const data = {
      from_date : this.from_date,
      to_date: this.to_date
  }
    this.userService.readshopmetricsbydate('/api/',data).subscribe(data => {
       this.shopsMetrics = data;
       for(let i=0;i<this.shopsMetrics.length;i++){
        shopkeypair.push(this.shopsMetrics[i].date);
        shopvalues.push(parseInt(this.shopsMetrics[i].count));
       }

    let shopmetricschart = new Chart({
      chart: {
        type: 'line'
      },
      title: {
        text: 'Shops Metrics'
      },
      xAxis: {
        categories: shopkeypair
    },
    yAxis: {
        title: {
            text: 'No of shops'
        }
    },
      credits: {
        enabled: false
      },
      series: [{
        name: 'Date',
        data: shopvalues
      }]
    });
    // chart.addPoint(4);
     this.shopmetricschart = shopmetricschart;
    // chart.addPoint(5);
    // setTimeout(() => {
    //   chart.addPoint(6);
    // }, 2000);
  });

  }

    getdashboardmetrics() {
      this.userService.readdashboardmetrics('/api/').subscribe(dashboarddata => {
          this.dashboardmetrics = dashboarddata;

      });
  }

    getdiscountMetricsbydate(from, to) {
        var discountkeypair = [];
        var discountvalues = [];

        if (from == null && to == null) {
            var days = 7; // Days you want to subtract
            var date = new Date();
            var last = new Date(date.getTime() - (days * 24 * 60 * 60 * 1000));
            var day = last.getDate();
            let d = '';
            var zero = 0;
            if(day < 10){
                d = ""+zero+day;
            }else {
                d = ""+day;
            }
            var month = last.getMonth() + 1;
            let m = '';
            if(month < 10){
               m = ""+zero+month;
            }else {
                m = ""+month
            }
            var year = last.getFullYear();
            this.from_date = year+"-"+m+"-"+d;
   
            let td = '';
            if(date.getDate() < 10){
                td = ""+zero+date.getDate();
            }else {
                td = ""+date.getDate();
            }
            let tm = '';
            if((date.getMonth()+1) < 10){
                tm = ""+zero+(date.getMonth()+1);
            }else {
                tm = ""+(date.getMonth()+1);
            }
   
            this.to_date = date.getFullYear()+"-"+tm+"-"+td;
        }else {
            this.from_date = from;
            this.to_date = to;
        }
        const data = {
            from_date : this.from_date,
            to_date: this.to_date
        }
        this.userService.readdiscountmetricsbydate('/api/', data).subscribe(data => {
            this.discountMetrics = data;

            for(let i=0;i<this.discountMetrics.length;i++){
                discountkeypair.push(this.discountMetrics[i].date);
                discountvalues.push(parseInt(this.discountMetrics[i].count));
               }

            let discountmetricschart = new Chart({
                chart: {
                    type: 'line'
                },
                title: {
                    text: 'Discount Metrics'
                },
                xAxis: {
                    categories: discountkeypair
                },
                yAxis: {
                    title: {
                        text: 'No of Discounts'
                    }
                },
                credits: {
                    enabled: false
                },
                series: [{
                    name: 'Date',
                    data: discountvalues
                }]
            });
            // chart.addPoint(4);
            this.discountmetricschart = discountmetricschart;
        })
    }

    getagentMetricsbydate(from, to) {
        var agentkeypair = [];
        var agentvalues = [];
        if (from == null && to == null) {
            var days = 7; // Days you want to subtract
            var date = new Date();
            var last = new Date(date.getTime() - (days * 24 * 60 * 60 * 1000));
            var day = last.getDate();
            let d = '';
            var zero = 0;
            if(day < 10){
                d = ""+zero+day;
            }else {
                d = ""+day;
            }
            var month = last.getMonth() + 1;
            let m = '';
            if(month < 10){
               m = ""+zero+month;
            }else {
                m = ""+month
            }
            var year = last.getFullYear();
            this.from_date = year+"-"+m+"-"+d;
   
            let td = '';
            if(date.getDate() < 10){
                td = ""+zero+date.getDate();
            }else {
                td = ""+date.getDate();
            }
            let tm = '';
            if((date.getMonth()+1) < 10){
                tm = ""+zero+(date.getMonth()+1);
            }else {
                tm = ""+(date.getMonth()+1);
            }
   
            this.to_date = date.getFullYear()+"-"+tm+"-"+td;
        }else {
            this.from_date = from;
            this.to_date = to;
        }
        const data = {
            from_date : this.from_date,
            to_date: this.to_date
        }
        this.userService.readusermetricsbydate('/api/', data).subscribe(data => {

            this.agentMetrics = data;

            for(let i =0; i<this.agentMetrics.length;i++){
                agentkeypair.push(this.agentMetrics[i].date); 
                agentvalues.push( parseInt(this.agentMetrics[i].count));
            }

            let usermetricschart = new Chart({
                chart: {
                    type: 'line'
                },
                title: {
                    text: 'User Metrics'
                },
                xAxis: {
                    categories: agentkeypair
                },
                yAxis: {
                    title: {
                        text: 'No of Users'
                    }
                },
                credits: {
                    enabled: false
                },
                series: [{
                    name: 'Date',
                    data: agentvalues
                }]
            });
            // chart.addPoint(4);
            this.usermetricschart = usermetricschart;
        })
    }

    getcouponMetricsbydate(from, to) {
        var couponkeypair = [];
        var couponvalues = [];
        if (from == null && to == null) {
            var days = 7; // Days you want to subtract
            var date = new Date();
            var last = new Date(date.getTime() - (days * 24 * 60 * 60 * 1000));
            var day = last.getDate();
            let d = '';
            var zero = 0;
            if(day < 10){
                d = ""+zero+day;
            }else {
                d = ""+day;
            }
            var month = last.getMonth() + 1;
            let m = '';
            if(month < 10){
               m = ""+zero+month;
            }else {
                m = ""+month
            }
            var year = last.getFullYear();
            this.from_date = year+"-"+m+"-"+d;
   
            let td = '';
            if(date.getDate() < 10){
                td = ""+zero+date.getDate();
            }else {
                td = ""+date.getDate();
            }
            let tm = '';
            if((date.getMonth()+1) < 10){
                tm = ""+zero+(date.getMonth()+1);
            }else {
                tm = ""+(date.getMonth()+1);
            }
   
            this.to_date = date.getFullYear()+"-"+tm+"-"+td;
        }else {
            this.from_date = from;
            this.to_date = to;
        }
        const data = {
            from_date : this.from_date,
            to_date: this.to_date
        }
        this.userService.readcouponmetricsbydate('/api/', data).subscribe(data => {
            this.couponMetrics = data;

            for(let i =0; i<this.couponMetrics.length;i++){
                couponkeypair.push(this.couponMetrics[i].date); 
                couponvalues.push( parseInt(this.couponMetrics[i].count));
            }

            let couponmetricschart = new Chart({
                chart: {
                    type: 'line'
                },
                title: {
                    text: 'Coupon Metrics'
                },
                xAxis: {
                    categories: couponkeypair
                },
                yAxis: {
                    title: {
                        text: 'No of Coupons'
                    }
                },
                credits: {
                    enabled: false
                },
                series: [{
                    name: 'Date',
                    data: couponvalues
                }]
            });
            // chart.addPoint(4);
            this.couponmetricschart = couponmetricschart;
        });
    }

}



