import { Injectable, EventEmitter, Output, Component, Input } from '@angular/core';
@Injectable()
export class agentSideBarService {

  isAgentOpen = false;

  @Output() agentchange: EventEmitter<boolean> = new EventEmitter();

  toggle() {
    this.isAgentOpen = !this.isAgentOpen;
    this.agentchange.emit(this.isAgentOpen);
  }

  agentSidebarClose() {
    this.agentchange.emit(false);
  }

  agentSidebarOpen() {
    this.agentchange.emit(true);
  }

}


