import { Component, Inject, ViewChild, OnInit } from '@angular/core';
import { UserService } from '../user.service';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import { MatTableDataSource, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { NgxSpinnerService } from 'ngx-spinner';
import * as $ from 'jquery';


@Component({
  selector: 'app-dshoplist',
  templateUrl: './dshoplist.component.html',
  styleUrls: ['./dshoplist.component.scss']
})
export class DshoplistComponent implements OnInit {

    status: any;
    shopsList: Array<any>;
    url = '/api/';
    dataSource: MatTableDataSource<any>;

    @ViewChild(MatSort) sort: MatSort;
    displayedColumns = ['name', 'phoneNumber', 'category', 'description', 'status'];
    applyFilter(filterValue: string) {
        filterValue = filterValue.trim(); // Remove whitespace
        filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
        this.dataSource.filter = filterValue;
    }

    constructor(private userService: UserService, private router: Router, public dialog: MatDialog, private spinner: NgxSpinnerService) {
        this.spinner.show();
        this.getShops();
    }

    public getShops() {
        const data = {
            status : 'ALL'
        }
        this.userService.readshops('/api/',data).subscribe(data => {
            this.shopsList = data.data;
            console.log(this.shopsList);
            this.dataSource = new MatTableDataSource(this.shopsList);
            this.dataSource.sort = this.sort;
            this.spinner.hide();
        });
    }


    ngOnInit(){
        
    }
    

}







