import { Component, HostBinding, OnInit, ViewChild, Inject, ElementRef, NgZone, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import {Location} from '@angular/common';
import { UserService } from '../user.service';
import { AbstractControl, FormControl, Validators, FormGroup, FormGroupDirective } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { MapsAPILoader } from '@agm/core';
import { createshopSideBarService } from '../createshop-sidebar/createshop-sidebar.service';

const valnumber = /^[0-9][0-9]*$/;
declare var google: any

@Component({
  selector: 'app-createshop-sidebar',
  templateUrl: './createshop-sidebar.component.html',
  styleUrls: ['./createshop-sidebar.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CreateshopSidebarComponent implements OnInit {
  @HostBinding('class.is-createshopOpen')
  isCreateshopOpen = false;
  /* this is for map */
  public latitude: number;
  public longitude: number;
  public searchControl: FormControl;
  public zoom: number;
  @ViewChild("search")
  public searchElementRef: ElementRef;
  private geoCoder;
  public categoriesList: any;
  fileToUpload: File = null;


  private base64textString: String = '';
  url = '/api/';
  cstatus: string;

  @ViewChild(FormGroupDirective) myform;

  // For Create shop
  createShopForm = new FormGroup({
    shopName: new FormControl('', Validators.compose([Validators.minLength(3),
    this.noWhitespaceValidator])),
    category: new FormControl('', Validators.compose([Validators.minLength(3),
    this.noWhitespaceValidator])),
    description: new FormControl('', Validators.compose([Validators.minLength(3),
    this.noWhitespaceValidator])),
    phoneNumber: new FormControl('', Validators.compose([Validators.maxLength(10),
    Validators.minLength(10), Validators.pattern(valnumber)])),
    timeFrom: new FormControl('', Validators.compose([Validators.minLength(3)])),
    timeTo: new FormControl('', Validators.compose([Validators.minLength(3)])),
    address: new FormControl('', Validators.compose([Validators.minLength(3),
    this.noWhitespaceValidator])),
    imageUrl: new FormControl(this.base64textString, Validators.compose([])),
    location: new FormControl(''),
    shopOwner: new FormControl('', Validators.compose([Validators.minLength(3),
    this.noWhitespaceValidator]))
});

public noWhitespaceValidator(control: FormControl) {
    let isWhitespace = (control.value || '').trim().length < 3;
    let isValid = !isWhitespace;
    return isValid ? null : { 'whitespace': true };
}

constructor(private userService: UserService, public dialog: MatDialog,
  private mapsAPILoader: MapsAPILoader, private location: Location, private ngZone: NgZone, 
  private cd: ChangeDetectorRef, private createshopsidebar: createshopSideBarService
) { }

ngOnInit() {

  this.createshopsidebar.createshopchange.subscribe(CreateshopOpen => {
    this.isCreateshopOpen = CreateshopOpen;
  })
  //this.createshopsidebar.createshopSidebarClose();

  // set google maps defaults
  this.zoom = 10;
  this.latitude = 17.4252392;
  this.longitude = 78.4252013;
  // create search FormControl
  this.searchControl = new FormControl();

  // set current position
  this.setCurrentPosition();
  this.getCategorieslist();

  // load Places Autocomplete
  this.mapsAPILoader.load().then(() => {
      let autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement, {
        types: ["address"]
      });
      this.geoCoder = new google.maps.Geocoder;
      autocomplete.addListener("place_changed", () => {
        this.ngZone.run(() => {
          // get the place result
          let place: any = autocomplete.getPlace();
          // verify result
          if (place.geometry === undefined || place.geometry === null) {
            return;
          }
          // set latitude, longitude and zoom
          this.latitude = place.geometry.location.lat();
          this.longitude = place.geometry.location.lng();
          this.zoom = 12;
        });
      });
    });



  }

  handleFileInput(files: FileList) {
    this.fileToUpload = files.item(0);
}

createshopclose(){
  this.createshopsidebar.createshopSidebarClose();
}

// Function For Create shop
onCreateShopSubmit() {
  let userdetails = {
      email: localStorage.getItem('useremail'),
      password: localStorage.getItem('password')
  }
  const shopdata = {
      name: this.createShopForm.get('shopName').value,
      category: this.createShopForm.get('category').value,
      description: this.createShopForm.get('description').value,
      phoneNumber: this.createShopForm.get('phoneNumber').value,
      timeFrom: this.createShopForm.get('timeFrom').value,
      timeTo: this.createShopForm.get('timeTo').value,
      address: this.createShopForm.get('address').value,
      image: this.fileToUpload,
      latitude: this.latitude,
      longitude: this.longitude,
      location: this.searchElementRef.nativeElement.value,
      //shop_owner: this.createShopForm.get('shopOwner').value,
     // status: 'W'
  };

  
  this.userService.createShop(userdetails, shopdata)
      .subscribe(
          (data) => {
              this.cstatus = 'Shop Created Successfully !';
          },
          (err) => {
              this.cstatus = '<span style="color:red">'+err.error.message+'</span>';
          }

      );
}

handleFileSelect(evt) {
  var files = evt.target.files;
  var file = files[0];

  if (files && file) {
      var reader = new FileReader();

      reader.onload = this._handleReaderLoaded.bind(this);

      reader.readAsBinaryString(file);
  }
}

_handleReaderLoaded(readerEvt) {
  console.log(readerEvt);
  var binaryString = readerEvt.target.result;
  this.base64textString = btoa(binaryString);
  console.log(this.base64textString);
}

private setCurrentPosition() {
  if ("geolocation" in navigator) {
    navigator.geolocation.getCurrentPosition((position) => {
      this.latitude = position.coords.latitude;
      this.longitude = position.coords.longitude;
      this.zoom = 12;
    });
  }
}

public markerEnd(markerDetails: any): void {
    if (markerDetails) {
        // console.info(markerDetails.coords);
        this.latitude = markerDetails.coords.lat;
        this.longitude = markerDetails.coords.lng;
        this.geoCoder.geocode({'location': {lat: this.latitude, lng: this.longitude }}, (results, status) => {
          if (status === 'OK') {
            if (results[0]) {
              this.searchElementRef.nativeElement.value = results[0].formatted_address;
              this.cd.detectChanges();
            } else {
              window.alert('No results found');
            }
          } else {
            window.alert('Geocoder failed due to: ' + status);
          }
        });
    }
}

getLocation() {
  if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
          this.latitude = position.coords.latitude;
          this.longitude = position.coords.longitude;
          
          this.geoCoder.geocode({'location': {lat: this.latitude, lng: this.longitude }}, (results, status) => {
              console.log(results);
              console.log(status);
              if (status === 'OK') {
                  if (results[0]) {
                      this.searchElementRef.nativeElement.value = results[0].formatted_address;
                      
                  } else {
                      window.alert('No results found');
                  }
              } else {
                  window.alert('Geocoder failed due to: ' + status);
              }

          });
      });
  } else { 
      console.log("Geolocation is not supported by this browser.");
  }
}

public getCategorieslist(){
  this.userService.readCategories('/api/').subscribe(data => {
      console.log(data);
      this.categoriesList = [];
      for (let i = 0; i < data.length; i++){
          this.categoriesList.push({value: data[i].text, viewValue: data[i].text});
      }
      this.cd.detectChanges();
      //console.log(this.categoriesList);

  });
}



}



