import { Component, OnInit, Inject, ViewChild, Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';
import { UserService } from '../user.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import {FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import { MatTableDataSource, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatTabChangeEvent} from '@angular/material';
import {DialogOverviewComponent} from '../createshop/createshop.component';
import * as $ from 'jquery';

@Component({
  selector: 'app-coupons',
  templateUrl: './coupons.component.html',
  styleUrls: ['./coupons.component.scss']
})
export class CouponsComponent implements OnInit {
  status: any;
  couponsList: Array<any>;
  couponList: Array<any>;
  url = '/api/';
  message: any;

  constructor(private userService: UserService, private spinner: NgxSpinnerService, private router: Router, 
    public dialog: MatDialog) { 
    this.spinner.show();
    this.couponsdata();
    }

    ngOnInit() {
      $('.mat-tab-label-container').css('background','#fff');
  
      //Close the dropdown if the user clicks outside of it
      window.onclick = function(event) {
        if (!(event.target as HTMLInputElement).matches('.dropbtn')) {
            var dropdowns = document.getElementsByClassName("dropdown-content");
            var i;
            for (i = 0; i < dropdowns.length; i++) {
                var openDropdown = dropdowns[i];
                if (openDropdown.classList.contains('show')) {
                    openDropdown.classList.remove('show');
                }
            }
        }
  
        
      }
    }

  couponsdata (){
    this.spinner.show();
  this.userService.readcoupons('/api/').subscribe(data => {
      this.couponsList = data;
      console.log(data);

      this.spinner.hide();
  });
}

showDropdown(id) {
  var dropdowns = document.getElementsByClassName("dropdown-content");
          
          for(var j = 0; j < dropdowns.length; j++){
              var openDropdown = dropdowns[j];
              if (openDropdown.classList.contains('show')) {
                  openDropdown.classList.remove('show');
              }
              
          }
  $(".myDropdown"+id).addClass('show');
  }

  updateCoupon(id){
    this.spinner.show();
        let selectedCoupon = this.couponsList.find(coupon => coupon.id == id);
        this.couponList = selectedCoupon;
        
        this.spinner.hide();
        this.editcouponDialog();
  }

  deleteCoupon(id){
    this.spinner.show();
    let reqData = {
        id: id
    }
    this.userService.deleteCouponById(reqData).subscribe(
        (data) => {
        this.message = "Coupon Deleted Successfully!";
        console.log(data);
        this.modifycouponDialog();
        this.spinner.hide();
        },
        (err) => {
            console.log(err);
            this.message = err.error.message;
            this.modifycouponDialog();
            this.spinner.hide();
        }

    );

  }

  modifycouponDialog(): void {
    const dialogRef = this.dialog.open(CouponslistActDactDialogComponent, {
        width: '450px',
        data:  this.message
    });

    dialogRef.afterClosed().subscribe(result => {
    });
}

  // Function to edit category data
  editcouponDialog(): void {
    const dialogRef = this.dialog.open(EditcouponsDialogComponent, {
        width: '450px',
        data:  this.couponList
    });
    console.log(this.couponList);

    dialogRef.afterClosed().subscribe(result => {
        // console.log('The dialog was closed');
        // this.cstatus = result;
    });
}

}

@Component({
  selector: 'app-dialog',
  templateUrl: 'editcoupon-dialog-component.html',
  styleUrls: ['./editcoupon-dialog-component.scss']
})
export class EditcouponsDialogComponent implements OnInit {
  cstatus: any;
  url = '/api/';
  categoryList: Array<any>;

  @ViewChild(FormGroupDirective) myform;
  // For Edit coupons
  editCouponForm = new FormGroup({
      couponid: new FormControl('', Validators.compose([Validators.minLength(3)])),
      description: new FormControl('', Validators.compose([Validators.minLength(3)])),
      conditions: new FormControl('', Validators.compose([Validators.minLength(3)])),
      validFrom: new FormControl('', Validators.compose([Validators.minLength(3)])),
      validTo: new FormControl('', Validators.compose([Validators.minLength(3)])),
      code: new FormControl('', Validators.compose([Validators.minLength(3)])),
      offPercentage: new FormControl('')
  });

  onEditCouponSubmit(){

    let validFrom = new Date(this.editCouponForm.get('validFrom').value);
    let fromDate = validFrom.getFullYear()+'-'+(validFrom.getMonth()+1)+'-'+validFrom.getDate();

    let validTo = new Date(this.editCouponForm.get('validTo').value);
    let toDate = validTo.getFullYear()+'-'+(validTo.getMonth()+1)+'-'+validTo.getDate();

    let reqdata = {
        id: this.editCouponForm.get('couponid').value,
        description: this.editCouponForm.get('description').value,
        conditions: this.editCouponForm.get('conditions').value,
        validFrom: fromDate,
        validTo: toDate,
        code: this.editCouponForm.get('code').value,
        offPercentage: parseInt(this.editCouponForm.get('offPercentage').value)   
    }
    console.log(reqdata);
    
    this.userService.editCouponbyid(this.url, reqdata)
        .subscribe(
            (data) => {
              this.cstatus = 'Coupon Updated Successfully !';
              this.updateDialog();
            },
            (err) => {
                console.log(err);
              this.cstatus = err.error.message;
              this.updateDialog();
            }

        );

  }

  updateDialog(): void {
    const dialogRef = this.dialog.open(DialogOverviewComponent, {
        width: '450px',
        data: { status: this.cstatus }
    });

    dialogRef.afterClosed().subscribe(result => {
        // this.cstatus = result;
    });
}
  ngOnInit() {
  }

  constructor(
    public dialogRef: MatDialogRef<EditcouponsDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, private userService: UserService, public dialog: MatDialog) { }

onNoClick(): void {
    this.dialogRef.close();
}
}

@Component({
    selector: 'app-dialog',
    templateUrl: 'couponActDact-dialog-component.html',
    styleUrls: ['./couponActDact-dialog-component.scss']
  })
  export class CouponslistActDactDialogComponent {
  
    constructor(
        public dialogRef: MatDialogRef<CouponslistActDactDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any) { }
  
    onNoClick(): void {
        this.dialogRef.close();
        location.reload();
    }
  
  }



