import { Component, OnInit, HostBinding, ChangeDetectorRef } from '@angular/core';
import {AbstractControl, FormControl, Validators, FormGroup, FormGroupDirective} from '@angular/forms';
import { categorySideBarService } from '../category-sidebar/category-sidebar.service';
import { subcategorySideBarService } from './subcategory-sidebar.service';
import { discountSideBarService } from '../discounts-sidebar/discounts-sidebar.service';
import { UserService } from '../user.service';

@Component({
  selector: 'app-subcategory-sidebar',
  templateUrl: './subcategory-sidebar.component.html',
  styleUrls: ['./subcategory-sidebar.component.scss']
})
export class SubcategorySidebarComponent implements OnInit {
  @HostBinding('class.is-Subopen')
  isSubOpen = false;
  status: any;
  subcategoriesList: Array<any>;
  subcategoryList: Array<any>;
  categoriesList: Array<any>;
  message: any;
  url = '/api/';
  cstatus: string;
  

  // For Create sub Category
  createSubCategoryForm = new FormGroup({
    subcategory: new FormControl('', Validators.compose([Validators.minLength(3),this.noWhitespaceValidator])),
    category:new FormControl('', Validators.compose([Validators.minLength(1)])),
});

public noWhitespaceValidator(control: FormControl) {
    let isWhitespace = (control.value || '').trim().length < 3;
    let isValid = !isWhitespace;
    return isValid ? null : { 'whitespace': true };
}

  constructor(private userService: UserService, private cd: ChangeDetectorRef, 
    private categorysidebar: categorySideBarService, private subcategorysidebar: subcategorySideBarService,
    private discountsidebar: discountSideBarService) { }

  ngOnInit() {
    this.categorysidebar.categorySidebarClose();
    this.subcategorysidebar.subcategorySidebarClose();
    this.discountsidebar.discountSidebarClose();
      this.categoriesdata('ALL');
    this.subcategorysidebar.schange.subscribe(isSubOpen => {
        this.isSubOpen = isSubOpen;
    });
  }

  // close create sub category menu
  subcategoryclose(){
    this.subcategorysidebar.subcategorySidebarClose();
  }

  categoriesdata (status){
      const data = {
        status : status
    }
    this.userService.readCategories('/api/').subscribe(data => {
        let cdata = data.filter(category => category.active == true);
        cdata = cdata.filter(category => category.parentid == null);
        this.categoriesList = [];
        for (let i = 0; i < cdata.length; i++){
            this.categoriesList.push({value: cdata[i].id, viewValue: cdata[i].text});
        }
        this.cd.detectChanges();
        
    });
  }

  // Function For Create category
  onCreateSubCategorySubmit(createSubCategoryForm,formDirective) {
    
    let reqdata = {
        parentId: this.createSubCategoryForm.get('category').value,
        text: this.createSubCategoryForm.get('subcategory').value
    }

    this.userService.createSubCategory(this.url, reqdata)
        .subscribe(
            (data) => {    
            this.cstatus = 'Sub Category Added Successfully !';
            formDirective.resetForm();
            createSubCategoryForm.reset();
            },
            (err) => {
                console.log(err.error);
            }

        );
}

}



