import { Injectable, EventEmitter, Output, Component, Input } from '@angular/core';
@Injectable()
export class subcategorySideBarService {

  isSubOpen = false;

  @Output() schange: EventEmitter<boolean> = new EventEmitter();

  toggle() {
    this.isSubOpen = !this.isSubOpen;
    this.schange.emit(this.isSubOpen);
  }

  subcategorySidebarClose() {
    this.schange.emit(false);
  }

  subcategorySidebarOpen() {
    this.schange.emit(true);
  }

}


