import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import {MatMenuTrigger} from '@angular/material';
import { APPCONFIG } from '../config';
import {UserService} from '../user.service';
import { categorySideBarService } from '../category-sidebar/category-sidebar.service';
import { subcategorySideBarService } from '../subcategory-sidebar/subcategory-sidebar.service';
import { discountSideBarService } from '../discounts-sidebar/discounts-sidebar.service';
import { couponSideBarService } from '../coupon-sidebar/coupons-sidebar.service';
import {agentSideBarService} from '../agent-sidebar/agent-sidebar.service';
import { createshopSideBarService } from '../createshop-sidebar/createshop-sidebar.service';
import { subscriptionplanSideBarService } from '../subscriptionplan-sidebar/subscriptionplan-sidebar.service'

@Component({
  selector: 'app-header',
  styles: ['./header.component.css'],
  templateUrl: './header.component.html'
})

export class AppHeaderComponent implements OnInit {
  public AppConfig: any;
  private categoryStatus: boolean = false;
  togglenav: boolean = false;

    constructor(private router: Router, private userservice: UserService, private categorysidebar: categorySideBarService,
    private subcategorysidebar: subcategorySideBarService, private discountsidebar: discountSideBarService, 
    private couponsidebar: couponSideBarService, private agentsidebar: agentSideBarService,
    private subscriptionplan: subscriptionplanSideBarService, private createshopsidebar: createshopSideBarService) {
    }

  categoryopen(){
    this.subcategorysidebar.subcategorySidebarClose();
    this.discountsidebar.discountSidebarClose();
    this.categorysidebar.categorySidebarOpen();
  }

  subcategoryopen(){
    this.categorysidebar.categorySidebarClose();
    this.discountsidebar.discountSidebarClose();
    this.subcategorysidebar.subcategorySidebarOpen();
  }

  discountopen(){
    this.categorysidebar.categorySidebarClose();
    this.subcategorysidebar.subcategorySidebarClose();
    this.discountsidebar.discountSidebarOpen();
  }

  couponopen(){
    this.closesidebars();
    this.couponsidebar.couponSidebarOpen();
  }

  agentopen(){
    this.closesidebars();
    this.agentsidebar.agentSidebarOpen();
  }

  createshopopen(){
    this.closesidebars();
    this.createshopsidebar.createshopSidebarOpen();

  }

  subscriptionpanopen(){
    this.closesidebars();
    this.subscriptionplan.subscriptionplanSidebarOpen();

  }

  closesidebars(){
    this.categorysidebar.categorySidebarClose();
    this.subcategorysidebar.subcategorySidebarClose();
    this.discountsidebar.discountSidebarClose();
    this.couponsidebar.couponSidebarClose();
    this.agentsidebar.agentSidebarClose();
    this.subscriptionplan.subscriptionplanSidebarClose();
    this.createshopsidebar.createshopSidebarClose();

  }

  ngOnInit() {
    this.AppConfig = APPCONFIG;

     if(localStorage.length === 0) {
         this.router.navigate(['/']);
     }
  }
    logout() {
        localStorage.clear();
        this.router.navigate(['/']);
    }

    togglenavbar(){
      this.togglenav = !this.togglenav;
    }
}



