import {RouterModule, Routes } from '@angular/router';

import { PageLoginComponent } from './login/login.component';
import {DashboardComponent} from './dashboard/dashboard.component';
import {CreateshopComponent} from './createshop/createshop.component';
import {ShopslistComponent} from './shopslist/shopslist.component';
import {DshoplistComponent} from './dshoplist/dshoplist.component';
import {DpendingapprovalsComponent} from './dpendingapprovals/dpendingapprovals.component';
import {VerifiedshopsComponent} from './verifiedshops/verifiedshops.component';
import {CreateagentComponent} from './createagent/createagent.component';
import {AgentslistComponent} from './agentslist/agentslist.component';
import {CreatecategoryComponent} from './createcategory/createcategory.component';
import {ViewcategoriesComponent} from './viewcategories/viewcategories.component';
import {ShopComponent} from './shop/shop.component';
import {AgentsComponent} from './agents/agents.component';
import {CategoriesComponent} from './categories/categories.component';
import {SubcategoriesComponent} from './subcategories/subcategories.component';
import {UsersComponent} from './users/users.component';
import {DiscountsComponent} from './discounts/discounts.component';
import {CouponsComponent} from './coupons/coupons.component';
import { SubscriptionsComponent } from './subscriptions/subscriptions.component';
import { SubscriptionPlanComponent } from './subscription-plan/subscription-plan.component';

const AppRoutes: Routes = [
   { path: '', component: PageLoginComponent },
   { path: 'dashboard', component: DashboardComponent },
   { path: 'createshop', component: CreateshopComponent },
   { path: 'shopslist', component: ShopslistComponent },
   { path: 'dshopslist', component: DshoplistComponent },
   { path: 'dpendingapprovals', component: DpendingapprovalsComponent },
   { path: 'verifiedshops', component: VerifiedshopsComponent },
   { path: 'createagent', component: CreateagentComponent },
   { path: 'agentslist', component: AgentslistComponent },
   { path: 'createcategory', component: CreatecategoryComponent },
   { path: 'viewcategory', component: ViewcategoriesComponent },
   { path: 'shops', component: ShopComponent },
   { path: 'agents', component: AgentsComponent },
   { path: 'categories', component: CategoriesComponent },
   { path: 'subcategories', component: SubcategoriesComponent },
   { path: 'users', component: UsersComponent },
   { path: 'discounts', component: DiscountsComponent },
   {path: 'coupons', component: CouponsComponent},
   {path: 'subscriptions', component: SubscriptionsComponent},
   {path: 'subscriptionplan', component: SubscriptionPlanComponent},
   { path: 'login', component: PageLoginComponent }
];

export const AppRoutingModule = RouterModule.forRoot(AppRoutes, {useHash: true});



