import { Injectable, EventEmitter, Output, Component, Input } from '@angular/core';
@Injectable()
export class createshopSideBarService {

  isCreateshopOpen = false;

  @Output() createshopchange: EventEmitter<boolean> = new EventEmitter();

  toggle() {
    this.isCreateshopOpen = !this.isCreateshopOpen;
    this.createshopchange.emit(this.isCreateshopOpen);
  }

  createshopSidebarClose() {
    this.createshopchange.emit(false);
  }

  createshopSidebarOpen() {
    this.createshopchange.emit(true);
  }

}


